import React, {Component} from "react";
import styled from "styled-components";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faSearch} from "@fortawesome/free-solid-svg-icons";
import {faWindowClose} from "@fortawesome/free-solid-svg-icons";

import { useParams, useLocation, useHistory, useRouteMatch } from 'react-router-dom'

const SearchField = (language) => {
    const history = useHistory();
    const handleClick = () => history.push('/goodbye');
    window.onkeypress=(el) => { if(el.key === 'Enter'){
        history.push(`/search/${document.getElementById("input_id")!=null?document.getElementById("input_id").value:""}`)
    } }
    return (
        <SearchFieldContainer>
         <Linke
              type='button'
              onClick={(el) => { 
                document.getElementById("input_id").value=""
              }}
            >
                <FontAwesomeIcon style={{color:"#47523f", }} icon={faWindowClose} size="10sm"/>
            </Linke>
                   <SearchInput  placeholder={language?"Запит...":"Query..."} id="input_id" />
         
                   <Linkes
              type='button'
              onClick={(el) => { 
               
                history.push(`/search/${document.getElementById("input_id")!=null?document.getElementById("input_id").value:""}`) }}

              
            >
                Знайти
            </Linkes>

    </SearchFieldContainer>
    );
  };
export default SearchField;
const Linkes = styled.button`
cursor: pointer;

padding: 12px 24px;
    min-width: 121px;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: .8px;
    font-weight: 600;
    color: #47523f;
    margin-left: 4px;
    border: 0;
    background-color: #edd7a2;    text-transform: uppercase;
    border-radius: 2px;
    media only screen and (max-width: 500px)
{
    padding: 12px;
    min-width: 97px;
}
    `
const Container =styled.div`
width: 100%;
height: 40px;
padding-left: 40px;
font-size: 16px;
line-height: 20px;
letter-spacing: .1px;
font-weight: 400;
border-radius: 2px;`
const Linke = styled.button`
position: absolute;
cursor: pointer;

    top: 0;
    left: 0;
    padding: 12px;
    background: 0 0;
    margin: 0;
    border: 0;
    `;
const SearchFieldContainer = styled.div`
position: relative;
    min-width: 552px;
    max-width: 552px;
    margin: 0 auto;
    line-height: 54px;
    display:flex;
    
    @media only screen and (max-width: 1090px)
    {
        min-width: 560px;
    }
    
    
    @media only screen and (max-width: 790px)
    {
        min-width: 400px;
    }
    @media only screen and (max-width: 500px)
     {
        min-width: 288px;
    margin: 0 16px;

    }
`;
//
const SearchInput = styled.input`
border: 0;
width: 100%;
    height: 40px;
    padding-left: 40px;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: .1px;
    font-weight: 400;
    border-radius: 2px;
    
  
`;
const SearchButtons = styled.div` 
color: white;
    
`;
