import React, {Component} from 'react'
import styled from "styled-components";
import NavBar from "./NavBar";
import {getNavLinks,getSocialLinks} from "../../api/queries";
import {generateNavLinksAction} from "../../Storage/NavSideLinksStorage/actions";
import {generateSocialLinksReceivedAction} from "../../Storage/LinksStorage/actions";
import "./index.css"
import BurgerImg from '../../imgs/iconfinder_Menu_4473024.svg';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faSearch,faTimes,faChevronLeft} from "@fortawesome/free-solid-svg-icons";
import {connect} from 'react-redux'
import SearchField from "./SerachField";
import SpecialFeatures from "./SpecialFeatures";
import LogoZsy from "../../imgs/logo.png"
import {Link} from "react-router-dom";
import {Helmet} from "react-helmet";

class Header extends Component{
   
    constructor(props) {
        super(props);
        this.state = {
            loader:false,
            activeDropdown: [],
            activeSearch:false,
            opened:false,
            sub_el:[],
            title:"",
            open:false,
            cat:"",
            sublistId:"",
            headerTop:{},
            lastPosition:0
    };

        this.toggleActiveDropdown = this.toggleActiveDropdown.bind(this);
        this.toggleActiveSearch = this.toggleActiveSearch.bind(this);
        this.toggleDisDropdown=this.toggleDisDropdown.bind(this);
        this.catShow=this.catShow.bind(this);
        this.setSubList=this.setSubList.bind(this);

    }
    setSubList(el){
        this.setState({
            sublistId:el
        })
    }
    catShow(el){
        this.setState({
            cat:el
        })
    }
    toggleActiveSearch(id){
        this.setState({
            activeDropdown:[],
            activeSearch: id,
            open:false,
        
        });
    }
    toggleActiveDropdown(id,sub_el,title){
        this.setState({
            activeSearch:false
            ,activeDropdown: sub_el,
            sub_el:sub_el,
            title:title
        });
    }
    toggleDisDropdown(id){
        this.setState({
            activeDropdown: [],
        });
        
    }
    
    render() {
        var ua=this.props.navLinks.ua;
        var en=this.props.navLinks.en;
        var menu;
        if(window.location.pathname.indexOf("/en")>=0){
            menu=en
        }else{
            menu=ua
        }
        
        return(
            <Container 
            onMouseOut={(el)=>{
                if(el.target.id==="men"||el.target.id==="drop"||el.target.id==="dropdown")
                    {

                    }else{
                        this.toggleDisDropdown()
                    }
            }}
            onMouseOver={(el)=>{
                if(el.target.id==="men"||el.target.id==="drop"||el.target.id==="dropdown")
                {
                }else{
                    this.toggleDisDropdown()
                }
            }}>
                 <Helmet>
 <meta charset="utf-8"/>
        <title>Офіційний портал Збройних Сил України</title>
        <meta name="description" content="Офіційний портал Збройних Сил України" />
        <meta name="keywords" content="Офіційний портал Збройних Сил України"  />
        <meta name="title" content="Офіційний портал Збройних Сил України" />

      </Helmet>
     
                <SecondCont id="header"  >            
                <NavBarToggle  onClick={() => {
                            this.dropDownMenu()
                                 }} >
                                     <IMG src={BurgerImg} />
                                 </NavBarToggle>
                <Logo >
                    <LogoA href={window.location.pathname.indexOf("/en")>=0?"/en":"/"} >
                    <LogoZsyImgWrapper>
                            <LogoImg src={LogoZsy}/>
                    </LogoZsyImgWrapper>
                    <Title>
                        <div>
                    {window.location.pathname.indexOf("/en")>=0?'Armed':'Збройні'}
                    </div>
                    <div style={{minWidth:"146px"}}>
                    {window.location.pathname.indexOf("/en")>=0?' Forces of Ukraine':' Cили України'}
                    </div>
                </Title>
                    </LogoA>
                </Logo>
                
                <DisabledOptionsWrapper>
                    <div style={{textAlign:"left",paddingBottom:"6px",color:"red"}}>
                    {window.location.pathname.indexOf("/en")>=0?'The site is in test mode':'Сайт працює в тестовому режимі'}
                        </div>
                    <SpecialFeatures
                        specialModeSwitch={this.props.specialModeSwitch}
                        language={window.location.pathname.indexOf("/en")>=0}
                        t={false}
                        languageModeSwitch={this.props.languageModeSwitch}
                        socialLinks={this.props.socialLinks}
                                          />
                    <div style={{textAlign:"left",paddingBottom:"16px",color:"red"}}><a style={{textAlign:"right",paddingBottom:"16px",color:"red",textDecoration:"none"}} target="_blank" href="https://www.mil.gov.ua/povidom-pro-korupcziyu.html">
                    {window.location.pathname.indexOf("/en")>=0?'A story about corruption':'Повідом про корупцію '}
                        </a></div>

                </DisabledOptionsWrapper>
                <SearchFiledWrapper>
                            <SearchButton onClick={(el) => {
                            this.toggleActiveSearch(!this.state.activeSearch)
                            
                                 }} style={{color:"#47523f", }} ><FontAwesomeIcon icon={faSearch}/></SearchButton>
                        </SearchFiledWrapper>
                </SecondCont>
                {
                    this.state.loader?<NavMenuWrapper id="drop">
                    <NavBar 
                    activeSearch={this.state.activeSearch}
                    activeDropdown={this.state.activeDropdown}
                    sub_el={this.state.sub_el}
                    title={this.state.title}
                    toggleActiveSearch={this.toggleActiveSearch} 
                    toggleActiveDropdown={this.toggleActiveDropdown} 
                    toggleDisDropdown={this.toggleDisDropdown} 
                    navLinks={menu} 
                    language={this.props.language} 
                    dropDownMenu={this.dropDownMenu}/>
                </NavMenuWrapper>
                :<NavMenuWrapper></NavMenuWrapper>
                }
                {
                     this.state.loader?<DropDown  open={this.state.open}>
                     {<div>
                        
                             <MobileMenu style={{background:"#47523F",listStyle:"none"}}>
                                 <MobileMenuJs open={this.state.open}>
                                     <MobileMenuContainer >
                                         <MobileMenuHeader>
                                             <MobileMenuLanguage>
                                                 <MobileMenuLanguageBut href={"/"} style={{color:window.location.pathname.indexOf("/en")>=0?"#909f84":"#ffffff", marginRight:"10px"}}>
                                                     УКР
                                                 </MobileMenuLanguageBut>
                                                 <MobileMenuLanguageBut href={"/en"} style={{color:window.location.pathname.indexOf("/en")>=0?"#ffffff":"#909f84"}}>
                                                   ENG  
                                                 </MobileMenuLanguageBut>
                                             </MobileMenuLanguage>
                                             <MobileMenuClose onClick={() => {
                            this.dropDownMenu()
                                 }}><FontAwesomeIcon icon={faTimes} color={"#fdfdfd"}/></MobileMenuClose>
                                         </MobileMenuHeader>
                                         <MobileMenuNav>
                                         <MobileMenuNavList>
                                 {                                
                                   menu.map((el) => {
                                         return(  <MobileMenuNavItem>
                                             {(el.title==="Новини")
                 ?<MobileMenuNavLink  onClick={this.dropDownMenu} style={{textDecoration: "none"}} href={`/All_news/ua`}>{el.title}</MobileMenuNavLink>
                 :(el.title==="Контакти")
                 ? <MobileMenuNavLink onClick={this.dropDownMenu} style={{textDecoration: "none"}} href={el.pageFK!=null?window.location.pathname.indexOf("/en")>=0?'/en/menu/'+el.pageFK:`/menu/`+el.pageFK:""} >Контакти</MobileMenuNavLink>
                 :
                 el.linkUrl!==null&&el.linkUrl!=="null"?<MobileMenuNavLink style={{textDecoration: "none"}} onClick={this.dropDownMenu} href={el.linkUrl} target="_blank"> {el.title}</MobileMenuNavLink> : <MobileMenuNavLink onClick={(ele)=>{this.catShow(el._id)}}> {el.title}</MobileMenuNavLink>  }
                                            <MobileMenuNavItemCategory open={this.state.cat===el._id}>
                                                <MobileMenuNavItemCategoryContainer>
                                                    <MobileMenuNavItemCategoryHeader>
                                                        <MobileMenuNavItemCategoryHeaderPrevBut onClick={(ele)=>{this.catShow("2")}}>
                                                        <FontAwesomeIcon icon={faChevronLeft} color={"#fdfdfd"}/> 
                                                        </MobileMenuNavItemCategoryHeaderPrevBut>
                                                        <MobileMenuNavItemCategoryHeaderNameCat>
                                                        {el.title}
                                                        </MobileMenuNavItemCategoryHeaderNameCat>
                                                        <MobileMenuNavItemCategoryHeaderCloseBut onClick={(ele)=>{
                                                            this.catShow("2")
                                                            this.dropDownMenu()
                                                            }} >
                                                           <FontAwesomeIcon icon={faTimes} color={"#fdfdfd"}/> 
                                                        </MobileMenuNavItemCategoryHeaderCloseBut>
                                                    </MobileMenuNavItemCategoryHeader>
                                                    <MobileMenuNavItemCategoryWrapper>
                                                    <MobileMenuNavItemCategoryList>
                                                   {el.child_sub?el.child_sub.map((sub_el)=>{
                                                       if(sub_el.pageFK===null||sub_el.pageFK==="null")
                                                       if (sub_el.child_sub===null||sub_el.child_sub==="null")
                                                       return null
                                                       return(
                                                           <MobileMenuNavItemCategoryItem className={(sub_el.title==="Відео")?"":(sub_el.title==="Фото")?"":(sub_el.content_id===null)?"has-sub":""}>
                                                                  {
                                                               sub_el.linkUrl? (sub_el.linkUrl!==null&&sub_el.linkUrl!=="null")? 
                                                                      <MobileMenuNavItemCategoryItemLink onClick={this.dropDownMenu} style={{textDecoration:"none",fontSize:"16px",lineHeight:"20px", fontWeight:"600", color:"#edd7a2",marginBottom:"20px",}} href={sub_el.link} target="_balnk">{sub_el.title}</MobileMenuNavItemCategoryItemLink>:  
                                                                      <MobileMenuNavItemCategoryItemLink onClick={this.dropDownMenu} style={{textDecoration:"none",fontSize:"16px",lineHeight:"20px", fontWeight:"600", color:"#edd7a2",marginBottom:"20px",}} href={sub_el.link} target="_balnk">{sub_el.title}</MobileMenuNavItemCategoryItemLink>:  
                                                                       (sub_el.title==="Історія війни")?
                                                                     <MobileMenuNavItemCategoryItemLink style={{textDecoration:"none"}} onClick={this.dropDownMenu} href={`/history_war`}>{sub_el.title}</MobileMenuNavItemCategoryItemLink>:
                                                                   
                                                                       (sub_el.title==="Нормативно-правова база")?
                                                                       <MobileMenuNavItemCategoryItemLink style={{textDecoration:"none"}}  onClick={this.dropDownMenu}  href={`/Нормативно-правова база`}>{sub_el.title}</MobileMenuNavItemCategoryItemLink>:
                                                                  
                                                                       (sub_el.pageFK===null||sub_el.pageFK==="null")?
                                                                       <MobileMenuNavItemCategoryItemLink onClick={(el)=>{
                                                                        this.setSubList(sub_el._id)
                                                                       }}  style={{textDecoration:"none"}}  >{sub_el.title}</MobileMenuNavItemCategoryItemLink>:      
                                                                       <MobileMenuNavItemCategoryItemLink style={{textDecoration:"none"}}  onClick={this.dropDownMenu} href={window.location.pathname.indexOf("/en")>=0?'/en/menu/'+sub_el.pageFK:`/menu/${sub_el.pageFK}`}
                                                                       
                                                                       >{sub_el.title}</MobileMenuNavItemCategoryItemLink>}
                                                                       {
                                                                         sub_el.child_sub?  
                                                                         <MobileMenuNavItemCategoryItemSubList open={this.state.sublistId===sub_el._id} length={sub_el.child_sub.length}>
                          {sub_el.child_sub?sub_el.child_sub.map((sub_sub_el)=>{
                              return(
                                  <MobileMenuNavItemCategoryItemSubListItem className={(sub_sub_el.title==="Історія війни")?"":(sub_sub_el.content_id===null)?"has-sub":""}>
                                      {
                                          
                                     sub_sub_el.linkUrl?(sub_sub_el.linkUrl!==null&&sub_sub_el.linkUrl!=="null")? 
                                     <MobileMenuNavItemCategoryItemSubListItemLink id="0" onClick={this.dropDownMenu} style={{textDecoration:"none",fontSize:"16px",lineHeight:"20px", fontWeight:"600",marginBottom:"20px",}} href={el.link} target="_balnk">{el.title}</MobileMenuNavItemCategoryItemSubListItemLink>:                 
                                     <MobileMenuNavItemCategoryItemSubListItemLink id="0" onClick={this.dropDownMenu} style={{textDecoration:"none",fontSize:"16px",lineHeight:"20px", fontWeight:"600",marginBottom:"20px",}} href={el.link} target="_balnk">{el.title}</MobileMenuNavItemCategoryItemSubListItemLink>:                 
                                    
                                     (sub_el.title==="Керівництво")?
                                     <MobileMenuNavItemCategoryItemSubListItemLink id="1" onClick={this.dropDownMenu} style={{textDecoration: "none", color: "#ececec"}} href={sub_sub_el.pageFK!=null?window.location.pathname.indexOf("/en")>=0?'/en/menu/'+sub_sub_el.pageFK:`/menu/`+sub_sub_el.pageFK:""}
                                      >{sub_sub_el.title}</MobileMenuNavItemCategoryItemSubListItemLink>:
                                         (sub_sub_el.title==="Нормативно-правова база")?
                                     <MobileMenuNavItemCategoryItemSubListItemLink id="2" onClick={this.dropDownMenu} style={{textDecoration: "none", color: "#ececec"}} href={`/Нормативно-правова база`}>{sub_sub_el.title}</MobileMenuNavItemCategoryItemSubListItemLink>:
                                     (sub_sub_el.title==="Корисні посилання")
                                      ?<MobileMenuNavItemCategoryItemSubListItemLink id="3" onClick={this.dropDownMenu}  style={{textDecoration: "none", color: "#ececec"}} href={`/Корисні посилання`}>{sub_sub_el.title}</MobileMenuNavItemCategoryItemSubListItemLink>
                                      :
                                      (sub_sub_el.pageFK===null||sub_sub_el.pageFK==="null")    
                                     ? null :
                                      <MobileMenuNavItemCategoryItemSubListItemLink style={{textDecoration: "none", color: "#ececec"}}id="5" onClick={this.dropDownMenu}  href={window.location.pathname.indexOf("/en")>=0?'/en/menu/'+sub_sub_el.pageFK:`/menu/${sub_sub_el.pageFK}`}>{sub_sub_el.title}</MobileMenuNavItemCategoryItemSubListItemLink>}
                                     
                                  </MobileMenuNavItemCategoryItemSubListItem>
                              )
                          }):null}
                      </MobileMenuNavItemCategoryItemSubList>
                                                                         :null
                                                                       }
                                                           </MobileMenuNavItemCategoryItem>
                                                       )
                                                   }):[]}
                                               </MobileMenuNavItemCategoryList>
                                           
                                                    </MobileMenuNavItemCategoryWrapper>
                                                </MobileMenuNavItemCategoryContainer>
                                            </MobileMenuNavItemCategory>
                                           </MobileMenuNavItem>)
                                       })
         }
                                 </MobileMenuNavList>
                             
                                         </MobileMenuNav>
                                         <MobileMenuFootter>
                                         <SpecialFeatures
                                             specialModeSwitch={this.props.specialModeSwitch}
                                             language={this.props.language}
                                             languageModeSwitch={this.props.languageModeSwitch}
                                             t={true}
                                             socialLinks={this.props.socialLinks}
                                          />    
                                         </MobileMenuFootter>

                                     </MobileMenuContainer>
                                 </MobileMenuJs>
                                  </MobileMenu>
                     </div>
                            
                         }
                     </DropDown>
                :<DropDown open={this.state.open}></DropDown>
                }
                {this.state.activeSearch?
                        <SearchDropDown style={{background:"#47523F"}} >
                        <div></div>
                        <SearchField   language={this.props.language}></SearchField>
                        
                    </SearchDropDown>
                        :<div></div>}
                
                
            </Container>
        )
        if(this.state.loader)
        return(<div></div>)
    }
    componentDidMount() {
       getNavLinks().then(navLinks => {
       
            this.props.setNavLinksToStore(navLinks);
            this.setState({
                loader:true
            })
        });
        getSocialLinks().then(socialLinks => {
            this.props.setSocialLinksToStore(socialLinks.filter(el=>el.type==="SOCIAL"));
        });
   
    this.setState({
        headerTop:document.getElementById('header')
    })

    window.addEventListener("wheel", (event) =>{
        var body = window.document.getElementById('root')
       var x = window.innerWidth|| body.clientWidth;
       console.log(x)
       if(x>1024){
        if (this.checkScrollDirectionIsUp(event)) {
            console.log('UP');
            this.state.headerTop.style.height="100px"
          } else {
            console.log('Down');
            this.state.headerTop.style.height="0px"

  
          }
        }else{
            this.state.headerTop.style.height="60px"
        }
      });
        
    }
     checkScrollDirectionIsUp(event) {
        if (event.wheelDelta) {
          return event.wheelDelta > 0;
        }
        return event.deltaY < 0;
      }

    dropDownMenu=()=>
        this.setState(()=>{      
      return {
            open:!this.state.open,
            activeSearch:false

        };
        })
    
}

const mapStateToProps = state => {
    return {
        navLinks: state.navSideLinksReducer.navLinks,
        socialLinks: state.socialLinksReducer.socialLinks,

    }
};

const mapDispatchToProps = dispatch => {
    return {
        setNavLinksToStore: (navLinks) => dispatch(generateNavLinksAction(navLinks)),
        setSocialLinksToStore: (socialLinks) => dispatch(generateSocialLinksReceivedAction(socialLinks)),

    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Header)
const MobileMenu = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    right:0;
    height: 100vh;
    width: 100vw;
    `
const MobileMenuJs = styled.div`
display: none;
position: relative;
background-color: #47523f;
@media only screen and (max-width: 1023px)
 {
    display: ${props => props.open ? "block" : "none"};
}
`
const MobileMenuContainer = styled.div`

`
const MobileMenuHeader = styled.div`
display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    height: 60px;
    margin-bottom: 20px;
`
const MobileMenuLanguage = styled.div`
line-height: 60px;
`
const MobileMenuLanguageBut = styled.a`
text-transform: uppercase;
text-decoration:none;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: .8px;
    font-weight: 600;
    color: #909f84;
    background: 0 0;
    margin: 0;
    padding: 0;
    border: 0;
`
const MobileMenuClose = styled.div`
cursor:pointer;
position: absolute;
top: 18px;
right: 16px;
height: 24px;
background: 0 0;
`


const MobileMenuNav = styled.nav`
margin: 0;
    padding: 0;
    border: 0;
    vertical-align: baseline;
`
const MobileMenuNavList = styled.ul`
list-style-type: none;
`
const MobileMenuNavItem = styled.li`
padding: 0 16px;
    height: 40px;
    border-bottom: 1px solid #394133;
`
const MobileMenuNavLink = styled.a`
-webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    text-transform: uppercase;
    font-size: 16px;
    line-height: 40px;
    letter-spacing: .8px;
    font-weight: 600;
    color: #edd7a2;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    :hover {
        text-decoration: underline !important;
        cursor: pointer!important;
    }
`
const MobileMenuNavIcon = styled.span`
height: 24px;

`
const MobileMenuNavItemCategory = styled.div`
 ${props => props.open ? "display:block" : "display:none"};
z-index: 200;
background-color: #47523f;
position: absolute;
    left: 0;
    top: 0;
    right:0
    height: 100vh;
    width: 100vw;
`
const MobileMenuNavItemCategoryContainer = styled.div`

`
const MobileMenuNavItemCategoryHeader = styled.div`
display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    height: 60px;
    margin-bottom: 20px;
`
const MobileMenuNavItemCategoryHeaderPrevBut = styled.button`
background: 0 0;
margin-left: 16px;
cursor: pointer;

    padding: 0;
    border: 0;
`
const MobileMenuNavItemCategoryHeaderCloseBut = styled.button`
background: 0 0;
    margin-right: 16px;
    padding: 0;
cursor: pointer;

    border: 0;
`
const MobileMenuNavItemCategoryHeaderNameCat = styled.div`
text-transform: uppercase;
    font-size: 16px;
    line-height: 16px;
    letter-spacing: .8px;
    font-weight: 600;
    color: #edd7a2;
    margin: 0;
    padding: 0;
    border: 0;
    vertical-align: baseline;
`
const MobileMenuNavItemCategoryWrapper = styled.div`
margin-bottom: 40px;
margin: 0;
    padding: 0;
    border: 0;
    vertical-align: baseline;
}
`
const MobileMenuNavItemCategoryList = styled.ul`
list-style-type: none;
margin: 0;
    padding: 0;
    border: 0;
    vertical-align: baseline;
`
const MobileMenuNavItemCategoryItem = styled.li`
margin: 0;
    padding: 0;
cursor: pointer;

    border: 0;
    vertical-align: baseline;
    .li.has-sub > a:after {
        display: block;
        content: " ";
        width: 0;
        height: 0;
    
    
        margin-right: -10px;
        
        right: 15px;
    }
`

const MobileMenuNavItemCategoryItemLink = styled.a`
:hover {
    text-decoration: underline !important;
}
padding: 0 16px;
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-webkit-box-pack: justify;
-ms-flex-pack: justify;
justify-content: space-between;
-webkit-box-align: center;
-ms-flex-align: center;
align-items: center;
font-size: 16px;
line-height: 40px;
        line-height: 30px;
        letter-spacing: .1px;
font-weight: 600;
color: #edd7a2;
border-bottom: 1px solid #394133;
`
const MobileMenuNavItemCategoryItemSubList = styled.ul`
max-height:  ${props => props.open? props.length*40:0 }px;
overflow: hidden;
-webkit-transition: max-height 300ms ease-out;
-o-transition: max-height 300ms ease-out;
transition: max-height 300ms ease-out;
list-style-type: none;
`
const MobileMenuNavItemCategoryItemSubListItem = styled.li`
:hover {
    text-decoration: underline;
}
padding: 0 16px;
background-color: #394133;

`
const MobileMenuNavItemCategoryItemSubListItemLink = styled.a`
display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding: 10px 0;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: .1px;
    font-weight: 600;
    color: #fdfdfd;
    text-decoration: none;
`
const MobileMenuFootter = styled.div`
display: -webkit-box;
display: -ms-flexbox;
display: flex;
padding-left:16px;
-webkit-box-align: center;
-ms-flex-align: center;
align-items: center;
height: 108px;
`

const SearchButton = styled.div` 
height: 60px;
margin-left: auto;
margin-right: 12px;
line-height: 52px;
background: 0 0;
cursor: pointer;
-webkit-transition: background-color 300ms;
-o-transition: background-color 300ms;
transition: background-color 300ms;
display:none;
@media only screen and (max-width: 1023px)
 {
    display: block;
}
`;
const IMG =styled.img`
height: 24px;
background: 0 0;
font-size: 24px;
`
const NavBarToggle = styled.div`
cursor: pointer;

display: none;
height: 24px;
margin-left: 16px;
    @media screen and (max-width: 1023px){
        display: block;

    }
`;
const LogoA =styled.a`
display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    text-decoration:none;
`
const Div =styled.div`
display:none;
@media only screen  and (max-width: 639px){
    display: block;
    text-align: center;
    padding: 10px;
    color: red;
    font-size: 10px;
}
`
const Menu =styled.div`
padding: 0; margin: 0; border: 0; width:100%
`;
const SearchDropDown = styled.div`
padding: 8px 0px 8px 0px;
-webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}
`
const DropDown=styled.div`
display: none;

@media only screen and (max-width: 1023px){
    display: ${props => props.open ? "block" : "none"};
    grid-area: r;
    width: 100%;
    margin-bottom: 5px;
        }
`;
const LogoZsyImgWrapper = styled.div`
height: 56px;
@media only screen and (max-width: 1023px)
 {
    height: 44px;
}
 
`;
const Logo = styled.div`
margin-left: 32px;
`
const Title = styled.div`
display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    margin-left: 12px;
    color: #202020;
    text-transform: uppercase;
    font-size: 18px !important;
    line-height: 22px;
    letter-spacing: .2px;
    font-weight: 600;
    
     @media screen and (max-width: 1023px){
        margin-left: 8px;
        font-size: 14px !important;
        line-height: 17px;
}
`;
const SecondCont= styled.div`
 display:flex;
 justify-content: space-between;
 position: relative;
 display: -webkit-box;
 display: -ms-flexbox;
 display: flex;
 -webkit-box-align: center;
 -ms-flex-align: center;
 align-items: center;
 top: 0;
 max-width: 1400px;
 height: 100px;
 margin: 0 auto;
 -webkit-transition: height 300ms ease-in-out;
 -o-transition: height 300ms ease-in-out;
 transition: height 300ms ease-in-out;

@media only screen and (max-width: 1023px)
 {
    display:  "flex" ;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    height: 60px;
    border-bottom: 2px solid #ececec;
}

`;

const Container = styled.header`
position: fixed;
top: 0;
background-color: #fdfdfd;
z-index: 500;
width: 100%;
    
    
`;
//

const LogoMinobImgWrapper = styled.div`
    grid-area: u;
    text-align: left;
        margin-left:10px;
        z-index:100;


`;


const LogoImg = styled.img`
o-object-fit: cover;
object-fit: cover;
-o-object-position: center;
object-position: center;
height: 100%;
    `;

const SearchFiledWrapper = styled.div`
height: 60px;
margin-left: auto;
margin-right: 12px;
line-height: 52px;
background: 0 0;
cursor: pointer;
-webkit-transition: background-color 300ms;
-o-transition: background-color 300ms;
transition: background-color 300ms;
display:none;
@media only screen and (max-width: 1023px)
 {
    display: block;
}
`;

const DisabledOptionsWrapper = styled.div`
top: 10px;
@media only screen and (max-width: 1023px)
 {
    display: none;
}

`;

const LinksWrapper = styled.div`
    grid-area: l;
    
    @media screen and (max-width: 1023px){
        display: none;
        width: 100vw;
    }
    z-index:100;

`;

