import React, {Component, Fragment} from "react";
import styled from "styled-components";
import {Link} from "react-router-dom";
import "../../fonts/mainStyle.css"
import {getMenuById} from"../../api/queries";
import {generatePageReceivedAction} from "../../Storage/MenuPageStorage/actions";
import {connect} from "react-redux";
import logo from "../../PublicComponents/assets/img/logo.png"
import NoImage from "../../imgs/ghzsy.png";
import Arroy from "../../imgs/arroy_left.svg"
import OL from "../../PublicComponents/styled/Ol"
import LI from "../../PublicComponents/styled/LI"
import MyComponent from 'react-fullpage-custom-loader'
import Logo from "../../imgs/logo.png"
import GhZsu from "../../imgs/ghzsy.png"
import {ADIS,AACT} from "../../PublicComponents/styled/A"

class VideoPage extends Component{
    constructor(props){
        super(props);
        this.state = {
            id:"",
            loading: true,
          };
    }
    componentDidMount() {
        const {id} = this.props.location.state|| {}
        getMenuById(this.props.match.params.id).then(page => {
            this.props.setPageToStore(page)
           this.setState({
            loading:false,
            id:id
           })
        })
    }

    renderFoto(){
        var cat;
          this.props.page.map(el=>{
              if(this.state.id===el._id){
                  cat=el
              }
          })
          return(<UL >
            {
cat.videos.map(el=>{
                 
  return (
    <LI class="video-gallery__item">
    <VideoGallery class="video-gallery__img-wrapper">
      <VideoGalleryInner class="video-gallery__img-inner">
          <NewsImg src={"https://www.youtube.com/embed/"+el.link.split("=")[1]} width="560" height="314" allow="fullscreen;"/>

      </VideoGalleryInner>
    </VideoGallery>
    <VideoGalleryText class="video-gallery__text-content">
      <H4 class="video-gallery__title">{el.title}</H4>
      <P class="video-gallery__date">{el.date}</P>
    </VideoGalleryText>
  </LI>
      )
       }
       )
            }
          </UL>)       
    }
    render() {
        if(this.state.loading)
        {
           return(
               <MyComponent wrapperBackgroundColor={"#ececec"}
               sentences={[""]}
               customLoader={
                 <Spenner>
                   <Logos  src={Logo}/>
                   <div>
                   Збройні Сили України
                   </div>
                 </Spenner>
              
               }
           />
           ) }
        window.scrollY=0
        var body = document.body;
        body.scroll(0.0,0.0)
        var cat;
        this.props.page.map(el=>{
            if(this.state.id===el._id){
                cat=el
            }
        })
        return (
            <div style={{background:"#FDFDFD",paddingBottom:"30px", textAlign: "left!important"}}>
                <OL >
                    <li style={{display: "flex"}}>
                        <ADIS  href="/">{"Головна"}</ADIS>
                        <img height={"12px"} width={"7px"} style={{margin:"6px 12px 6px 12px"}} src={Arroy}/>
                    </li>
                    <LI style={{display: "flex"}}>
                        <ADIS  >{"Медіа"}</ADIS>
                        <img height={"12px"} width={"7px"} style={{margin:"6px 12px 6px 12px"}} src={Arroy}/>
                    </LI>
                    <LI style={{display: "flex"}}>
                        <ADIS  href={`/menu/video`}>Відеогалерея</ADIS>
                        <img height={"12px"} width={"7px"} style={{margin:"6px 12px 6px 12px"}} src={Arroy}/>
                    </LI>
                    <LI style={{color:"#909F84", fontSize:"16px"}}>
                        <AACT>{cat.title_ua}</AACT>
                    </LI>
                </OL>
               <PrimaryNewsWrapper>
               <Container class="container">
            <PageMedia class="page-media__content">
              <Title class="page-media__title"> {cat.title_ua}</Title>
              {this.renderFoto()}
               
              </PageMedia>
          </Container>
         
              
           
            
          
            </PrimaryNewsWrapper>
                        </div>
        );
    }
}

let mapStateToProps = state => {
    return {
        page: state.menuPageReducer.page,

    }
};

let mapDispatchToProps = dispatch => {
    return {
        setPageToStore: page => dispatch(generatePageReceivedAction(page)),


    }
};

export default connect(mapStateToProps, mapDispatchToProps)(VideoPage);
