import React from "react";
import image from "./img/logo.png";
import animate from "./libs/animate.css"
import styles from "./libs/styles.css";

class MainLoader extends React.Component {
  render() {
    return (
      <div className='MainLoaderBG' id="22">
        <div
          className='MainLoaderWrapper animated rotate rotateIn'
          id='MainLoaderWrapper'>
          <img src={image} className='loading-img' alt='loading...' />
        </div>
      </div>
    );
  }
   
  async componentDidMount() {
    const DURATION_ROTATE = 500;
    const DURATION_FADEOUT = 500;
    let wrapper = document.getElementById("MainLoaderWrapper");
    let w=document.createElement("a");
    let we=document.getElementById("22");


            setTimeout(() => {
                let intervalId = setInterval(this.scrollStep.bind(this), this.props.delayInMs);
      wrapper.classList.remove("rotateIn");
      setTimeout(() => {

        wrapper.classList.add("fadeOut");
        setTimeout(() => {
          wrapper.remove();
            we.appendChild(w);
        }, DURATION_FADEOUT);
      }, 0);
    }, DURATION_ROTATE)
    ;

  }
}

export default MainLoader;
