import React, {Component} from 'react'
import styled from 'styled-components'
import UsefulLinks from './UsefulLinks'
import {getContacts, getFooterLinks,getSocialLinks} from "../../api/queries";
import {generateFooterLinksReceivedAction} from "../../Storage/FooterStorage/actions";
import {generateSocialLinksReceivedAction} from "../../Storage/LinksStorage/actions";
import {connect} from "react-redux";
import {generateContactsReceivedAction} from "../../Storage/ContactsStorage/actions";
import Iconfacebook from "../../imgs/facebook";
import Icontwitter from "../../imgs/twitter";
import Iconyoutube from "../../imgs/youtube";
import Iconinstagram from "../../imgs/instagram";
let usefulLinks = {
    titleUa: "Посилання на інформаційні ресурси ЗС України",
    titleEn:"Links to information resources of the Armed Forces of Ukraine"

};
let militaryLinks = {
    titleUa: "Корисні посилання",
    titleEn:"Useful links"
};
let ourPartners={
    titleUa:"Наші партнери",
    titleEn:"Our partners"
}
let foot1={
    titleUa:"©${} Збройні Сили України",
    titleEn:"©2021 Armed Forces of Ukraine "
}
let foot2={
    titleUa:"ß-версія. Деякі розділи сайту працюють у тестовому режимі.",
    titleEn:"ß-version. Some sections of the site work in test mode."
}
let foot3={
    titleUa:"Карта сайту",
    titleEn:"Map site"
}

class Footer extends Component{
    componentDidMount() {

       
        getFooterLinks().then(footerLinks=>{
            this.props.setFooterLinksToStore(footerLinks)
        });
        getContacts().then(contacts=>{
            this.props.setContactsToStore(contacts)
        })
        getSocialLinks().then(socialLinks => {
            this.props.setSocialLinksToStore(socialLinks.filter(el=>el.type==="SOCIAL"));
        });

    }
    renderContacts(contacts){
        return(
            <FooterContacts>
                {contacts.map(el=>{
                    if(el.type==="Телефон") {
                        return (
                            <Phone>{el.contact}</Phone>
                        )
                    }
                    if(el.type==="Адреса") {
                        return (
                            <Address>{el.contact}</Address>
                        )
                    }
                    if(el.type==="Пошта") {
                        return (
                            <Email>{el.contact}</Email>
                        )
                    }
                })}
            </FooterContacts>
        )
    }
    render() {
        return(
            <MainContainer>
            <Container >
                <FooterTop>
                    <FooterContact>
                        {this.renderContacts(this.props.contacts)}
                        <FooterSocial>
                            <FooterSocials>
                            {this.props.socialLinks.map(el=>
                {
                    if(el.title_en==="facebook")
                        return(
                            <A href={el.link} target="_blank">
                                    <Iconfacebook  width={40}   height={20} fill = "#EDD7A2"></Iconfacebook>
                                </A>
                            )
                        if(el.title_en==="twitter")
                            return(
                                <A href={el.link} target="_blank">
                                    <Icontwitter  width={40}   height={20} fill = "#EDD7A2"></Icontwitter>
                                </A>
                            )
                        if(el.title_en==="youtube")
                            return(
                                <A href={el.link} target="_blank">
                                    <Iconyoutube  width={40}   height={20} fill = "#EDD7A2"></Iconyoutube>
                                </A>
                            )
                            if(el.title_en==="instagram")
                            return(
                                <A href={el.link}  target="_blank">
                                    <Iconinstagram  width={40}   height={20} fill = "#EDD7A2"></Iconinstagram>
                                </A>
                            )
                } )}
                            </FooterSocials>
                        
                        </FooterSocial>
                    </FooterContact>
                    <FooterList>
                    <MilitaryLinksWrapper>
                    <UsefulLinks language={window.location.pathname.indexOf("/en")>=0} 
                    links={this.props.footerLinks.filter(el => el.type === "USEFUL")}/>
                </MilitaryLinksWrapper>
                <UsefulLinksWrapper>
                    <UsefulLinks language={window.location.pathname.indexOf("/en")>=0} 
                    links={this.props.footerLinks.filter(el => el.type === "QUALITY")}/>
                </UsefulLinksWrapper>
                
                    </FooterList>
                </FooterTop>
                <FooterBottom>
                <MapSite>
                    <P><a style={{
                        textDecoration:"none",
                        color: "#edd7a2",
                    }} href={`${window.location.pathname.indexOf("/en")>=0?"/en/mapsite":"/mapsite"}`}>{window.location.pathname.indexOf("/en")>=0?foot3.titleEn:foot3.titleUa}</a></P>
                </MapSite>
                <Title>
                    {window.location.pathname.indexOf("/en")>=0?`©${new Date().getFullYear()} Armed Forces of Ukraine `:`©${new Date().getFullYear()} Збройні Сили України`}
                </Title>
                </FooterBottom>
                

            </Container>
            </MainContainer>
        )
    }

} 

const mapStateToProps = state => {
    return {
        footerLinks:state.footerReducer.footerLinks,
        contacts:state.contactsReducer.contacts,
        socialLinks: state.socialLinksReducer.socialLinks,

    }
};

const mapDispatchToProps = dispatch => {
    return {
        setContactsToStore:(contacts)=>dispatch(generateContactsReceivedAction(contacts)),
        setFooterLinksToStore:(footerLinks)=>dispatch(generateFooterLinksReceivedAction(footerLinks)),
        setSocialLinksToStore: (socialLinks) => dispatch(generateSocialLinksReceivedAction(socialLinks)),

    }
};
export default connect(mapStateToProps, mapDispatchToProps)(Footer);
const A =styled.a`
will-change: contents;
    -webkit-transition: -webkit-transform 300ms;
    -o-transition: transform 300ms;
    transition: transform 300ms;
    transition: transform 300ms,-webkit-transform 300ms;
    text-decoration:none;
`
const FooterSocials = styled.div`
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-webkit-box-pack: justify;
-ms-flex-pack: justify;
justify-content: space-between;
-webkit-box-align: center;
-ms-flex-align: center;
align-items: center;
min-width: 104px;
`
