export let getNewsEn=async (ofset,limit,date,category) => {
    console.log(ofset,limit,date,category)
    console.log(`/en?offset=${ofset}&limit=${limit}${date!=""?"&date="+date:""}${category!="All news"&&category!=""&&category!="Всі новини"?"&category="+category:""}`)
    let response = await fetch(`${NEWS_URL}/en?offset=${ofset}&limit=${limit}${date!=""?"&date="+date:""}${category!="All news"&&category!=""&&category!="Всі новини"?"&category="+category:""}`);
    let links = await response.json();
    return links || [];
};
