import React, {Component} from "react";
import styled from "styled-components";
import "codemirror/lib/codemirror.css";
import {getSideMenuById} from "../../api/queries";
import {Link} from "react-router-dom";
import {FaArrowCircleUp} from "react-icons/fa";
import Arroy from "../../imgs/arroy_left.svg"
import OL from "../../PublicComponents/styled/Ol"
import LI from "../../PublicComponents/styled/LI"
import {generatePageReceivedAction} from "../../Storage/MenuPageStorage/actions";
import {connect} from "react-redux";
import MyComponent from 'react-fullpage-custom-loader'
import Logo from "../../imgs/logo.png"
class SideMenuContentPage extends Component{

    constructor(props){
        super(props);
        window.scrollTo(0,0);

        this.state = {
            loading: true,
            show:false
        };
        this.toggleShow =this.toggleShow.bind(this);

    }
   
    componentDidMount() {
        window.scrollTo(0,0);
        const { id } = this.props.match.params
        getSideMenuById(id).then(page => {
            this.props.setPageToStore(page.content)
            this.setState({
                loading:false,
               
            })
        })
        window.addEventListener('wheel',this.handleScroll);
        var body = document.body;
        body.scroll(0.0,0.0)
    }
    toggleShow(){
        this.setState({show:!this.state.show});
        document.body.scroll(0.0,0.0)
    }
    handleScroll = () => {
        const scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
        if (scrollTop >300 ) {
            this.setState({activeDropdown: null,  show:true});

            // здесь манипуляции с this.props.history
        } else if (scrollTop <= 300 ) {
            this.setState(  {show:false});

            // здесь манипуляции с this.props.history
        }
        this.lastScroll = scrollTop;
    };
    render() {
        if(this.state.loading)
        {
           return(
               <MyComponent wrapperBackgroundColor={"#ececec"}
               sentences={[""]}
               customLoader={
                 <Spenner>
                   <Logos  src={Logo}/>
                   <div>
                   Збройні Сили України
                   </div>
                 </Spenner>
              
               }
           />
           ) }
        let today=false;
        let date = new Date(this.props.page?this.props.page.date:"");
             let nowDate = new Date ;
        if(nowDate.getDate() === date.getDate() && nowDate.getMonth() === date.getMonth() )
        {
            today=true;
        }
        else{
  
        }

        return(
            <div style={{background:"#FDFDFD",paddingBottom:"30px"}}>
                 <Div>
                <OL style={{gridArea:"a"}}>
                <li>
                <Link style={{textDecoration:"none",color:"#000000", fontSize:"16px"}} to="/">{this.props.language?"Main":"Головна"}</Link>
                <img height={"12px"} width={"7px"} style={{margin:"0px 12px 0px 12px"}} src={Arroy}/>

                </li>             
                <LI style={{color:"#909F84", fontSize:"16px"}}>
                {this.props.page.page_title?this.props.page.page_title:""}
                </LI>
                </OL>
                </Div>
                <Container>
           

           <Butoon onClick={this.toggleShow} opened={this.state.show}>
               <FaArrowCircleUp size={"2em"} style={{color:"#384038"}}/>
            </Butoon>           
            <div style={{textAlign:"left",gridArea:"b",padding:"32px",textAlign:"left !important",alignItems:"left !important" }}>      
            <div style={{paddingBottom:"48px"}}>
                <div style={{fontSize:"2em",  textAlign:"left",fontWeight:"700",textAlign:"left",paddingBottom:"16px"}}>{this.props.page?this.props.page.page_title:""}</div>
                <div >
                <DateItem>{((date.getUTCDate())<10?('0'+date.getUTCDate()):(date.getUTCDate()))} {" "}
            {this.getMonth(date)} {date.getFullYear()+  "р."} / {((date.getHours())<10)?('0'+date.getHours()):(date.getHours())}:{(date.getMinutes()<10)?('0'+date.getMinutes()):(date.getMinutes())}</DateItem>
                             </div>
                </div>        

                <Mains  dangerouslySetInnerHTML ={{ __html: this.props.page?this.props.page.html_body:"" }}/>
            </div>
           </Container>
           </div>
        )
        window.scrollTo(0,0);

    }
    getMonth(date){
        if(date.getMonth()+1===1){
            return this.props.language?"january":"січень"
        }if(date.getMonth()+1===2){
            return this.props.language?"february":"лютий"
            
        }if(date.getMonth()+1===3){
            return (this.props.language?"marth":"березня")
            
        }if(date.getMonth()+1===4){
            return this.props.language?"april":"квітень"
            
        }if(date.getMonth()+1===5){
            return this.props.language?"may":"травень"
            
        }if(date.getMonth()+1===6){
            return this.props.language?"june":"червень"
            
        }if(date.getMonth()+1===7){
            return this.props.language?"july":"липень"
            
        }if(date.getMonth()+1===8){
            return this.props.language?"august":"серпень"
            
        }if(date.getMonth()+1===9){
            return this.props.language?"september":"вересень"
            
        }if(date.getMonth()+1===10){
            return this.props.language?"october":"жовтень"
            
        }if(date.getMonth()+1===11){
            return this.props.language?"november":"листопад"
            
        }if(date.getMonth()+1===12){
            return this.props.language?"december":"грудень"
            
        }
        return null;

    }   getMonth(date){
        if(date.getMonth()+1===1){
            return this.props.language?"january":"січня"
        }if(date.getMonth()+1===2){
            return this.props.language?"february":"лютого"
            
        }if(date.getMonth()+1===3){
            return (this.props.language?"marth":"березня")
            
        }if(date.getMonth()+1===4){
            return this.props.language?"april":"квітня"
            
        }if(date.getMonth()+1===5){
            return this.props.language?"may":"травня"
            
        }if(date.getMonth()+1===6){
            return this.props.language?"june":"червня"
            
        }if(date.getMonth()+1===7){
            return this.props.language?"july":"липня"
            
        }if(date.getMonth()+1===8){
            return this.props.language?"august":"серпня"
            
        }if(date.getMonth()+1===9){
            return this.props.language?"september":"вересня"
            
        }if(date.getMonth()+1===10){
            return this.props.language?"october":"жовтня"
            
        }if(date.getMonth()+1===11){
            return this.props.language?"november":"листопада"
            
        }if(date.getMonth()+1===12){
            return this.props.language?"december":"грудня"
            
        }
        return null;

    }

}
let mapStateToProps = state => {
    return {
        page: state.menuPageReducer.page,
    }
};

let mapDispatchToProps = dispatch => {
    return {
        setPageToStore: page => dispatch(generatePageReceivedAction(page)),
    }
};
export default connect(mapStateToProps, mapDispatchToProps) (SideMenuContentPage);
