class HistoryOfWar extends Component{
    constructor(props){
        super(props);
      this.state={
        id:"",
        year:{},
        events:[],
        titles:"",
        loading:true
      }
      
       
    }
    componentDidMount(){
       
        getNavLinks().then(navLinks => {
            this.props.setNavLinksToStore(navLinks);
            var ua=navLinks.ua;
            var en=navLinks.en;
            this.props.language?en.map((el)=>{
               if(el.child_sub!=null){
                el.child_sub.map((sub_item)=>{
                    if(sub_item.title.toLowerCase()==="історія війни"){
                        this.setState({
                            id:sub_item.pageFK
                        })
                     
                    }

                })
               }
            }):ua.map((el)=>{
                if(el.child_sub!=null){
                 el.child_sub.map((sub_item)=>{
                     if(sub_item.title.toLowerCase()==="історія війни"){
                         this.setState({
                             id:sub_item.pageFK
                         })
                      
                     }
 
                 })
                }
             })
            getMenuById(this.state.id).then(page => {
                this.props.setPageToStore(page)
                page.shared_pages.map(el=>{
                    getMenuById(el.id).then(year => {
                        this.props.setPageToStore(year)
                        year.shared_pages.map(event=>{
                            if(event.id===this.props.match.params.id)
                            {

                                this.setState({
                                    year:el,
                                    events:year.shared_pages,
                                    titles:event.title,
                                    loading:false
                                })

                            }
                        })
                        
                    })
                })
                
            })
            getHistoryWar(this.props.match.params.id).then(history => {
    
                this.props.setHistoryToStore(history)
                this.setState({
                    loading:false
                })
                
               
            })
            
           
        });
        
      
    }
   
    render() {
        if(this.state.loading)
        {
           return(
               <MyComponent wrapperBackgroundColor={"#ececec"}
               sentences={[""]}
               customLoader={
                 <Spenner>
                   <Logos  src={Logo}/>
                   <div>
                   Збройні Сили України
                   </div>
                 </Spenner>
              
               }
           />
           ) }


        if(this.props.history!==null){
            let today=false;
        let date = new Date(this.props.history.publishDate);
             let nowDate = new Date ;
        if(nowDate.getDate() === date.getDate() && nowDate.getMonth() === date.getMonth() )
        {
            today=true;
        }
        else{
  
        }
        return(
            <div style={{background:"#ffffff"}}>
                 <Helmet>
        <title>{this.props.history.title}</title>
        <meta name="description" content={this.props.history.title} />
        <meta name="keywords" content={`Історія війни,${this.props.history.title},${this.props.history.title}`} />
        <meta name="title" content={this.props.history.title} />

      </Helmet>
              
               <OL>
               <li style={{display: "flex"}}>
                   <ADIS style={{textDecoration:"none", color:"#000000",marginRight:"0.5em" , fontSize:"16px"}} to="/">{this.props.language?"Main":"Головна"}</ADIS>
                    <img height={"12px"} width={"7px"} style={{margin:"6px 12px 6px 12px"}} src={Arroy}/>
           
               </li>
               <li style={{display: "flex"}}>
                   <ADIS  >{this.props.language?"Main":"ООС"}</ADIS>
                    <img height={"12px"} width={"7px"} style={{margin:"6px 12px 6px 12px"}} src={Arroy}/>
           
               </li>
               <li style={{display: "flex"}}>
               <ADIS s to={`/menu/${this.state.id}`}>{this.props.language?"History of war":"Історія війни"}</ADIS>
                    <img height={"12px"} width={"7px"} style={{margin:"6px 12px 6px 12px"}} src={Arroy}/>
           
               </li>
            
           <li>
              <AACT>{this.props.history.title}</AACT>
           </li>

           </OL>
           
             
              
            
            <Container>
       
       
       <Butoon onClick={this.toggleShow} opened={this.state.show}>
           <FaArrowCircleUp size={"2em"} style={{color:"#384038"}}/>
        </Butoon>           
        <Cont style={{}}>      
            <TitlePage >{this.props.history?this.props.history.title:""}</TitlePage>
            <div >
            <DateItem>{((date.getUTCDate())<10?('0'+date.getUTCDate()):(date.getUTCDate()))} {" "}
        {this.getMonth(date)} {date.getFullYear()+  "р."} / {((date.getHours())<10)?('0'+date.getHours()):(date.getHours())}:{(date.getMinutes()<10)?('0'+date.getMinutes()):(date.getMinutes())}</DateItem>
                         </div>
            
                         <ContInner>
                                <Article>
                                <Mains title ={this.props.history.title} dangerouslySetInnerHTML ={{ __html: this.props.history?this.props.history.html_body:"" }}/>

                                </Article>

                            </ContInner>
        
        </Cont>
      
        </Container>
        
            </div>
           
        )
        }
    }
    getMonth(date){
        if(date.getMonth()+1===1){
            return this.props.language?"january":"січня"
        }if(date.getMonth()+1===2){
            return this.props.language?"february":"лютого"
            
        }if(date.getMonth()+1===3){
            return (this.props.language?"marth":"березня")
            
        }if(date.getMonth()+1===4){
            return this.props.language?"april":"квітня"
            
        }if(date.getMonth()+1===5){
            return this.props.language?"may":"травня"
            
        }if(date.getMonth()+1===6){
            return this.props.language?"june":"червня"
            
        }if(date.getMonth()+1===7){
            return this.props.language?"july":"липня"
            
        }if(date.getMonth()+1===8){
            return this.props.language?"august":"серпня"
            
        }if(date.getMonth()+1===9){
            return this.props.language?"september":"вересня"
            
        }if(date.getMonth()+1===10){
            return this.props.language?"october":"жовтня"
            
        }if(date.getMonth()+1===11){
            return this.props.language?"november":"листопада"
            
        }if(date.getMonth()+1===12){
            return this.props.language?"december":"грудня"
            
        }
        return null;

    }
}

let mapStateToProps = state => {
    return {
        history: state.historyPageReducer.history,
        navLinks: state.navSideLinksReducer.navLinks,
        page: state.menuPageReducer.page,


    }
};

let mapDispatchToProps = dispatch => {
    return {
        setHistoryToStore: history => dispatch(generateHistoryReceivedAction(history)),
        setPageToStore: page => dispatch(generatePageReceivedAction(page)),

        setNavLinksToStore: (navLinks) => dispatch(generateNavLinksAction(navLinks)),

    }
};
export default connect(mapStateToProps, mapDispatchToProps) (HistoryOfWar);

