class ENHistoryWarPage extends Component{
    constructor(props){
        super(props);
      this.state={
        id:"",
        loading:true
      }
      
       
    }
    componentDidMount(){
        
        getNavLinks(this.props.language?'en':'ua').then(navLinks => {
            this.props.setNavLinksToStore(navLinks);
            var ua=navLinks.ua;
            var en=navLinks.en;
            this.props.language?en.map((el)=>{
               if(el.child_sub!=null){
                el.child_sub.map((sub_item)=>{
                    if(sub_item.title.toLowerCase()==="історія війни"){
                        this.setState({
                            id:sub_item.pageFK
                        })
                     
                    }

                })
               }
            
            
            
            }):ua.map((el)=>{
                if(el.child_sub!=null){
                 el.child_sub.map((sub_item)=>{
                     if(sub_item.title.toLowerCase()==="історія війни"){
                         this.setState({
                             id:sub_item.pageFK,
                             
                         })
                      
                     }
 
                 })
                }
             
             
             
             })
            getMenuById(this.state.id).then(page => {
                this.props.setPageToStore(page)
                this.setState({
                    loading:false
                   })
               
            })
            getHistoryWar(this.props.match.params.id).then(history => {
    
                this.props.setHistoryToStore(history)
               this.setState({
                loading:false
               })
            })
        });
        
      
    }
   
    render() {
        if(this.state.loading)
        {
           return(
               <MyComponent wrapperBackgroundColor={"#ececec"}
               sentences={[""]}
               customLoader={
                 <Spenner>
                   <Logos  src={Logo}/>
                   <div>
                   Збройні Сили України
                   </div>
                 </Spenner>
              
               }
           />
           ) }
        return (
            <div style={{background:"#FDFDFD",paddingBottom:"30px"}}>
                <Helmet>
        <title>Історія війни {this.props.history.title}</title>
        <meta name="description" content={`Історія війни ${this.props.history.title}`} />
        <meta name="keywords" content={`Історія війни ${this.props.history.title}`} />
        <meta name="title" content={`Історія війни ${this.props.history.title}`} />
      </Helmet>
                <OL style={{gridArea:"a"}}>
                <li>
                <Link style={{textDecoration:"none",color:"#000000", fontSize:"16px"}} to="/">{this.props.language?"Main":"Головна"}</Link>
                <img height={"12px"} width={"7px"} style={{margin:"0px 12px 0px 12px"}} src={Arroy}/>
                </li>
                <li>
                <a style={{textDecoration:"none",color:"#000000", fontSize:"16px"}} >ООС</a>
                <img height={"12px"} width={"7px"} style={{margin:"0px 12px 0px 12px"}} src={Arroy}/>
                </li>
                <li>
                <Link style={{textDecoration:"none", color:"#909F84", fontSize:"16px"}} to={`/menu/${this.state.id}`}>{this.props.language?"History of war":"Історія війни"}</Link>
                <img height={"12px"} width={"7px"} style={{margin:"0px 12px 0px 12px"}} src={Arroy}/>
                </li>
                <LI style={{color:"#909F84"}}>
                    <Link style={{textDecoration:"none", color:"#909F84", fontSize:"16px"}} >{this.props.history.title}</Link>
                </LI>
                
                </OL>   
            <Container>
            <Card>
            <TiTle>
                            Роки
                        </TiTle>
                        <PageSubMenu>
                                <SubNavList>
                                {this.props.page!==null?this.props.page.shared_pages!=null?this.props.page.shared_pages.map(el=>
                            {
                                if(this.props.history.title===el.title){
                                    return(
                                        <SubNavItem style={{fontWeight:"400"}}>
                                                                            <SubNavCard>
                                                                            <SubNavLink style={{color:"#EDD7A2" ,background: "#47523F" }} >
                                                                                    <SubNavTitle>
                                                                                        {el.title}
                                                                                        <SubNavIcon>
                                                                                        
                                                                                        </SubNavIcon>
                                                                                    </SubNavTitle>
                                                                                </SubNavLink>
                                                                            </SubNavCard>
                                                                        </SubNavItem>
                                        
                                    )
                                }else{
                                    return(
                                        <SubNavItem style={{fontWeight:"400"}}>
                                        <SubNavCard>
                                        <SubNavLink href={`/history_war/${el.id}`} style ={{ color: "#000000",textDecoration:  "none",}} >
                                                <SubNavTitle>
                                                    {el.title}
                                                    <SubNavIcon>
                                                    
                                                    </SubNavIcon>
                                                </SubNavTitle>
                                            </SubNavLink>
                                        </SubNavCard>
                                    </SubNavItem>
                                       )
                                }
                                
                            }
                            ):null:null}
                               
                                </SubNavList>
                            </PageSubMenu>
                        
                                                 
                                                               
                                                                   
                    </Card>
                 
                <Dive>
                    <InfoContainer>
                    {this.props.history!==null?<div style={{padding:"32px"}}>
                                        <TittleRen id={`${this.props.history.title}`}>{this.props.history.title}</TittleRen>
                                            {this.props.history.shared_pages!=null?this.props.history.shared_pages.map(el=>
                                                 <NewsItemWrapper>
                                                 <NewsItemsContainer>
                                                     <div>
                                                         <Link style={{textDecoration:"none"}}  to={`/year/${el.id}`}>
                                                         <TitleItem>{el.title} </TitleItem></Link>
                                                     </div>
     
                                                 </NewsItemsContainer>
                                             </NewsItemWrapper>
                                                ):null}
                                        </div>
                     :null}                 
                   
                   
                    </InfoContainer>
                   
                </Dive>
            
            
            </Container>
            </div>
        );
    }
}
let mapStateToProps = state => {
    return {
        history: state.historyPageReducer.history,
        navLinks: state.navSideLinksReducer.navLinks,
        page: state.menuPageReducer.page,


    }
};

let mapDispatchToProps = dispatch => {
    return {
        setHistoryToStore: history => dispatch(generateHistoryReceivedAction(history)),
        setPageToStore: page => dispatch(generatePageReceivedAction(page)),

        setNavLinksToStore: (navLinks) => dispatch(generateNavLinksAction(navLinks)),

    }
};
export default connect(mapStateToProps, mapDispatchToProps) (ENHistoryWarPage);
