let limit=7;
class ENFotoPage extends Component{
    constructor(props){
        super(props);
        this.state = {
            id:"",
            loading: true,
            isOpen: false,
            image:""
          };
    }
    componentDidMount() {
        const {id} = this.props.location.state|| {}
        getMenuById(this.props.match.params.id).then(page => {
            var cat;
          page.map(el=>{
              if(id===el._id){
                  cat=el
              }
          })
            this.props.setPageToStore(cat.photos.reverse())
           this.setState({
            loading:false,
            id:cat.title_en
           })
        })
        window.addEventListener('wheel', ()=>{

            if (document.documentElement.scrollTop || document.body.scrollTop > document.body.scrollHeight/2-100) {
                limit+=1
                this.setState({})
              }
        });
        
        
    }

    handleShowDialog = (image) => {
        this.setState({ isOpen: !this.state.isOpen,image:image });
      };
    renderFoto(){
        var cat;
          return this.props.page.slice(0,limit).map((el) => {
            
                return (
                    
                    <NewsItemWrapper >
                        <NewsImg id={el} onClick={()=>{
                          this.handleShowDialog(el)
                        }} src={`/image/${el}`}/>
                        
                    </NewsItemWrapper>
                )
   
            
           
       }
       
  )
    }
    render() {
        if(this.state.loading)
        {
           return(
               <MyComponent wrapperBackgroundColor={"#ececec"}
               sentences={[""]}
               customLoader={
                 <Spenner>
                   <Logos  src={Logo}/>
                   <div>
                   Збройні Сили України
                   </div>
                 </Spenner>
              
               }
           />
           ) }
        window.scrollY=0
        var body = document.body;
        body.scroll(0.0,0.0)
        var cat;
        
        return (
            <div style={{background:"#FDFDFD",paddingBottom:"30px", textAlign: "left!important"}}>
                {this.state.isOpen && (
<div style={{
    width: "100%",
    height: "100%",
    left: "0",
    top: "0",
    zIndex: "1042",
    overflow: "hidden",
    position: "fixed",
    background: "#0b0b0b",
    opacity: ".8",
}}></div>
)}
                {this.state.isOpen && (
          <dialog
            className="dialog"
            style={{ position: 'absolute' }}
            open
            onClick={this.handleShowDialog}
          >
            <MFPWrap >
                <MFPContainer >
                    <MFPContent >
                        <MFPFigure >
                            <Button title="Close (Esc)" type="button"  onClick={this.handleShowDialog}>×</Button>
                            <Figure>
                                <Imge src={"/image/"+this.state.image} />
                                   
                                                    </Figure>
                                                    </MFPFigure>
                                                    </MFPContent>
                                                    </MFPContainer>
                                                    </MFPWrap>
          </dialog>
        )}
                <Div>
                <OL style={{gridArea:"a"}}>
                <li style={{display: "contents"}}>
                <ADIS  href="/en">{"Main"}</ADIS>
                <img height={"12px"} width={"7px"} style={{margin:"6px 12px 6px 12px"}} src={Arroy}/>

                </li>
             
                <LI style={{display: "contents"}}>
                <ADIS >{"Media"}</ADIS>
                <img height={"12px"} width={"7px"} style={{margin:"6px 12px 6px 12px"}} src={Arroy}/>

                </LI>
                <LI style={{display: "contents"}}>
                <ADIS  href={`/en/menu/photo-galery`}>Photo</ADIS>
                <img height={"12px"} width={"7px"} style={{margin:"6px 12px 6px 12px"}} src={Arroy}/>
                </LI>
                <LI style={{color:"#909F84", fontSize:"16px"}}>
                <AACT>{this.state.id}</AACT>
                </LI>
                
                </OL>
                </Div>
               <PrimaryNewsWrapper>
              <Containerer>
              
               {this.renderFoto()}
           
            
            </Containerer>
            </PrimaryNewsWrapper>
                        </div>
        );
    }
}

let mapStateToProps = state => {
    return {
        page: state.menuPageReducer.page,

    }
};

let mapDispatchToProps = dispatch => {
    return {
        setPageToStore: page => dispatch(generatePageReceivedAction(page)),


    }
};

export default connect(mapStateToProps, mapDispatchToProps)(ENFotoPage);
