import React, { Component } from 'react';
import styled from "styled-components";
import Slider from "react-slick";
import "./index_fullscreen.css"
import ArroyLeft from "../../imgs/left-arrow.png"
import ArroyRight from "../../imgs/right-arrow.png"

function SampleNextArrow(props) {
    const { className, style, onClick } = props;
    return (
        <div
            className={className}
            style={{ ...style, display: "block" }}
            onClick={onClick}
        >
            <img width="30px" src={ArroyRight}/>

        </div>
    );
}

function SamplePrevArrow(props) {
    const { className, style, onClick } = props;
    return (
        <div
            className={className}
            style={{ ...style, display: "block" }}
            onClick={onClick}
        >
            <img width="30px" src={ArroyLeft}/>

        </div>
    );
}
class FullScreenSlider extends Component {
    constructor(props) {
        super(props);

        this.handleEvent = this.handleEvent.bind(this);
    }
    handleEvent() {
        console.log(this.props);
    }
    renderNewsCard(images) {
        return images.map(el => {
            return (
                <div>
                    <img width="100%" onClick={() => {
                        this.handleShowDialog(el, images.indexOf(el))
                    }} src={`/image/${el}`} />
                </div>
            )
        });
    }
    newrenderNewsCard(image) {
        return image.map(el => {
            return (
                <div>
                    <IMG src={`/image/${el}`} />
                </div>
            )
        });
    }

    render() {
        return (
            <dialog className="qwrqweqw"
                style={{
                    width: "100%",
                    height: "100%",
                    left: "0",
                    top: "0",
                    zIndex: "1042",
                    overflow: "hidden",
                    position: "fixed",
                    background: "#fff",
                    borderStyle: "unset",
                    borderWidth: "0px"
                
                }}
                open
            >
                <MFPWrap >
                    <MFPContainer >
                        <MFPContent >
                            <Button title="Close (Esc)" type="button" onClick={this.props.handleShowDialog}>×

                            </Button>
                            <Figure>
                                <Slider {...{

                                    nextArrow: <SampleNextArrow />,
                                    prevArrow: <SamplePrevArrow />,
                                    beforeChange: (prev, next) => {
                                        this.setState({ currentSlide: next });
                                    },

                                    arrows: true,
                                    dots: true,
                                    infinite: true,
                                    speed: 500,
                                    slidesToShow: 1,
                                    slidesToScroll: 1,
                                    initialSlide: this.props.image

                                }}  >

                                    {this.newrenderNewsCard(this.props.slider)}
                                </Slider>

                            </Figure>

                        </MFPContent>
                    </MFPContainer>
                </MFPWrap>
            </dialog>
        );
    }
}
export default FullScreenSlider;

