import {ADIS,AACT} from "../../../PublicComponents/styled/A"
class ENVideoPage extends Component{
    constructor(props){
        super(props);
        this.state = {
            id:"",
            loading: true,
          };
    }
    componentDidMount() {
        const {id} = this.props.location.state|| {}
        getMenuById(this.props.match.params.id).then(page => {
            this.props.setPageToStore(page)
           this.setState({
            loading:false,
            id:id
           })
        })
    }

    renderFoto(){
        var cat;
          this.props.page.map(el=>{
              if(this.state.id===el._id){
                  cat=el
              }
          })
          return(<div style={{padding:"0px 32px ",gridArea:"f"}}>
            {
cat.videos.map(el=>{
                 
  return (
      <div>
            <NewsItemWrapper  >
           
           <NewsImg src={"https://www.youtube.com/embed/"+el.link.split("=")[1]} width="560" height="314" allow="fullscreen;">
          
           </NewsImg>
           <div style={{gridArea:"d"}}>
           
          <FioCont >
               <Fio ><b style={{textDecoration:"none",display:'inline-block'}}>{el.title}</b></Fio>
               <Fio style={{fontSize:"16px",fontWeight:"400"}}><b style={{textDecoration:"none",display:'inline-block'}}>{el.date}</b></Fio>
           </FioCont>
      
               </div>
           <br/>
       </NewsItemWrapper>
       <div style={{borderBottom:"1px solid grey", width:"100%"}}></div>
       </div>)
       }
       )
            }
          </div>)       
   
            
           
       
    }

    render() {
        if(this.state.loading)
        {
           return(
               <MyComponent wrapperBackgroundColor={"#ececec"}
               sentences={[""]}
               customLoader={
                 <Spenner>
                   <Logos  src={Logo}/>
                   <div>
                   Збройні Сили України
                   </div>
                 </Spenner>
              
               }
           />
           ) }
        window.scrollY=0
        var body = document.body;
        body.scroll(0.0,0.0)
        var cat;
        this.props.page.map(el=>{
            if(this.state.id===el._id){
                cat=el
            }
        })
        return (
            <div style={{background:"#FDFDFD",paddingBottom:"30px", textAlign: "left!important"}}>
                <Div>
                <OL >
                <li style={{display: "flex"}}>
                <ADIS href="/en">{"Main"}</ADIS>
                <img height={"12px"} width={"7px"} style={{margin:"0px 12px 0px 12px"}} src={Arroy}/>

                </li>
             
                <LI style={{display: "flex"}}>
                <ADIS  >{"Media"}</ADIS>
                <img height={"12px"} width={"7px"} style={{margin:"0px 12px 0px 12px"}} src={Arroy}/>

                </LI>
                <LI style={{display: "flex"}}>
                <ADIS href={`/menu/video`}>Video gallery</ADIS>
                <img height={"12px"} width={"7px"} style={{margin:"0px 12px 0px 12px"}} src={Arroy}/>

                </LI>
                <LI style={{color:"#909F84", fontSize:"16px"}}>
                    <AACT>
                    {cat.title_en}
                    </AACT>
                </LI>
                
                </OL>
                </Div>
               <PrimaryNewsWrapper>
              
               {this.renderFoto()}
           
            
          
            </PrimaryNewsWrapper>
                        </div>
        );
    }
}

let mapStateToProps = state => {
    return {
        page: state.menuPageReducer.page,

    }
};

let mapDispatchToProps = dispatch => {
    return {
        setPageToStore: page => dispatch(generatePageReceivedAction(page)),


    }
};

export default connect(mapStateToProps, mapDispatchToProps)(ENVideoPage);

