class HistoryOfWar extends Component{
    toggleActiveDropdown(id){
        this.setState({activeDropdown: id});
    }

    toggleOpen(){
        this.setState({opened: !this.state.opened});
    }
    clickButton= async (url) => {
       await fetch(url)
    }
    constructor(props){
        super(props);

        this.state = {
            activeDropdown: null,
            opened: false,
            loading:true
        };
      
        this.toggleActiveDropdown = this.toggleActiveDropdown.bind(this);
        this.toggleOpen = this.toggleOpen.bind(this);
        this.clickButton = this.clickButton.bind(this);

    }
    componentDidMount(){
        getRegulatoryPage().then(regulatory => {
            this.props.setPageToStore(regulatory)
            this.setState({
                loading:false
            })
        })
    }
    render() {
        if(this.state.loading)
        {
           return(
               <MyComponent wrapperBackgroundColor={"#ececec"}
               sentences={[""]}
               customLoader={
                 <Spenner>
                   <Logos  src={Logo}/>
                   <div>
                   Збройні Сили України
                   </div>
                 </Spenner>
              
               }
           />
           ) }
        return (
            <div style={{background:"#FDFDFD",paddingBottom:"30px"}}>
               <Div>
                <OL style={{gridArea:"a"}}>
                <li>
                <Link style={{textDecoration:"none",color:"#000000", fontSize:"16px"}} to="/">{this.props.language?"Main":"Головна"}</Link>
                <img height={"12px"} width={"7px"} style={{margin:"0px 12px 0px 12px"}} src={Arroy}/>
                </li>
                <li>
                <a style={{textDecoration:"none",color:"#000000", fontSize:"16px"}} >Корисне</a>
                <img height={"12px"} width={"7px"} style={{margin:"0px 12px 0px 12px"}} src={Arroy}/>
                </li>
                <LI style={{color:"#909F84"}}>
                    <Link style={{textDecoration:"none", color:"#909F84", fontSize:"16px"}}>{this.props.language?"Regulatory framework":"Нормативно-правова база"}</Link>
                </LI>
                
                </OL>
                </Div>
                
            <Container>
            <ContainerTitle >{this.props.language?"Regulatory framework":"Нормативно-правова база"}</ContainerTitle>   
                <Dive>
                    <InfoContainer>
                    <NavBarContainer id={"navbar"}>
                        <NavBarDropdownWrapper opened={this.state.opened}>
                                {this.renderDropdowns(this.props.regulatory)}
                        </NavBarDropdownWrapper>
                    </NavBarContainer>
                    </InfoContainer>
                </Dive>
            </Container>
            </div>
        );
    }
    renderDropdowns(data){
        return data.redirect_pages?data.redirect_pages.map((redirect_page, index) => {
            let active = this.state.activeDropdown === index;
            const Button = withRouter(({ history }) => (
                <div
                  type='button'
                  onChange={(el) => { history.push(``) }}
                />
              ))
            return (
                <NavBarDropdown
                    onMouseLeave={() => {
                        this.setState({activeDropdown: null})
                    }}
                    key={index}
                >
                    <NavDropdownTitle
                        onClick={() => {
                            this.toggleActiveDropdown(index)
                        }}

                        onMouseEnter={() => {
                            this.toggleActiveDropdown(index)
                        }}

                        active={active}
                    >
                        <img src={Folder} height={"24px"} width={"20px"}/>
                        <div style={{paddingLeft:"10px",textDecoration: "none", color: "black", fontSize: "16px",lineHeight:"24px"}} >{redirect_page.title}</div>
                    </NavDropdownTitle>

                    <NavBarDropdownItemContainer active={active}>

                        {
                        
                        redirect_page.files?redirect_page.files.map((file, index) => (
                            <a key={index} target="_blank"  href={file.link} style={{textDecoration: "underline", color: "black", fontSize: "16px",lineHeight:"24px",fontWeight:"500"}} download>
                                
                                <NavBarDropdownItem>
                                    <img src={File} height={"24px"} width={"16px"}/>
                                   <div style={{paddingLeft:"10px"}}>{file.title}</div> 
                                    </NavBarDropdownItem>
                                
                                </a>
                        )):null}

                    </NavBarDropdownItemContainer>
                </NavBarDropdown>
            )
            }
        ):null
    }
}
let mapStateToProps = state => {
    return {
        regulatory: state.regulatoryPageReducer.regulatory,
    }
};

let mapDispatchToProps = dispatch => {
    return {
        setPageToStore: regulatory => dispatch(generateRegulatoryReceivedAction(regulatory)),
    }
};
export default connect(mapStateToProps, mapDispatchToProps)(HistoryOfWar);

