import navSideLinksReducer from './NavSideLinksStorage/reducer'
import newsReducer from "./NewsStorage/reducer"
import forcesReducer from "./ForcesStorage/reducer"
import footerReducer from "./FooterStorage/reducer"
import socialLinksReducer from "./LinksStorage/reducer"
import blocksReducer from "./BlocksStorage/reducer"
import commandsReducer from './CommandsStorage/reducer'
import contactsReducer from "./ContactsStorage/reducer"
import categoryReducer from "./CategoryStorage/reducer"
import idReducer from "./IDStorage/reducer"
import one_newReducer from "./NewStorage/reducer"
import menuPageReducer from "./MenuPageStorage/reducer"
import historyPageReducer from "./HistoryStorage/reducer"
import regulatoryPageReducer from "./RegulatoryStorage/reducer"
import pagesReducer from "./PagesStorage/reducer"


import {combineReducers} from "redux";

//change to combineReducers
export default combineReducers(
    {
        navSideLinksReducer,
        newsReducer,
        forcesReducer,
        footerReducer,
        socialLinksReducer,
        blocksReducer,
        commandsReducer,
        contactsReducer,
        categoryReducer,
        idReducer,
        one_newReducer,
        menuPageReducer,
        historyPageReducer,
        regulatoryPageReducer,
        pagesReducer

            }
    );
