import React, {Component, Fragment} from 'react';
import {BrowserRouter, Route, Switch, Link} from 'react-router-dom'
import Main from "./Pages/Main/Main";
import Header from "./PublicComponents/Header/Header";
import Footer from "./PublicComponents/Footer/Footer";
import styled from "styled-components";
import NotFoundPage from "./Pages/NotFoundPage";
import NewsPage from "./Pages/NewsPage/NewsPage";
import AllNewsPage from "./Pages/NewsPage/AllNewsPage"
import MainLoader from "./PublicComponents/MainLoader/MainLoader";
import MainMenuContentPage from "./Pages/NewsPage/MainMenuContentPage";
import SearchedPage from "./Pages/NewsPage/SearchedPage";
import FotoPage from "./Pages/NewsPage/FotoPage";
import VideoPage from "./Pages/NewsPage/VideoPage";
import { createBrowserHistory as history} from 'history';
import SideMenuContentPage from"./Pages/NewsPage/SideMenuContentPage";
import ENMain from "./Pages/Main/en/ENMain";
import AllNewsPageParams from './Pages/NewsPage/AllNewsPageParams';
import HistoryOfWar from './Pages/NewsPage/HistoryWarPage';
import HistoryOfWaPage from './Pages/NewsPage/HistoryPage';
import ENNewsPage from './Pages/NewsPage/en/ENNewsPage'
import ENMainMenuContentPage from './Pages/NewsPage/en/ENMainMenuContentPage'
import ENAllNewsPageParams from './Pages/NewsPage/en/ENAllNewsPageParams'
import ENHistoryPage from './Pages/NewsPage/en/ENHistoryPage'
import ENHistoryWarPage from './Pages/NewsPage/en/ENHistoryWarPage'
import ENFotoPage from './Pages/NewsPage/en/ENFotoPage'
import ENVideoPage from './Pages/NewsPage/en/ENVideoPage'
import {Helmet} from "react-helmet";
import RegulatoryFramework from './Pages/NewsPage/RegulatoryFramework';
import UsefulLinksPage from './Pages/NewsPage/UsefulLinksPage';
import DownloadPage from './Pages/NewsPage/DownloadPage'
import MapSitePage from './Pages/map_site'

import RssPage from './Pages/NewsPage/Rss'
import MyComponent from 'react-fullpage-custom-loader'
import Logo from "./imgs/logo.png"
class App extends Component {
  constructor(props) {
    super(props);
  }
  fakeRequest = () => {
    return new Promise(resolve => setTimeout(() => resolve(), 100));
  };
  state = {
    specialMode: false,
    loading: true,
    language:false,
    show:true,
    lastScrollTop:0
  };
    specialModeSwitch = () =>
      this.setState(() => {
          return { specialMode: !this.state.specialMode };
      });
    languageModeSwitch=()=>
    this.setState(()=>{
      return {language:!this.state.language};
    })
    languageModeSwitcher=(val)=>
    this.setState(()=>{
      return {language:val};
    })
    switchLoading=(value)=> {
      this.setState(()=>{
      return {loading:value};
    })
  }
    handleClick() {
    }
    componentDidMount() {
      this.fakeRequest().then(() => {
         this.setState({ loading: false }); // showing the app
      });
    }
    render() {
        const { specialMode,language } = this.state;
        if(this.state.loading)
        {
           return(
               <MyComponent wrapperBackgroundColor={"#ececec"}
               sentences={[""]}
               customLoader={
                 <Spenner>
                   <Logos  src={Logo}/>
                   <div>
                   Збройні Сили України
                   </div>
                 </Spenner>
               }/>
           )
         }
        
      return (
        <RenderData specialMode = {specialMode} language={language} switchLoading={this.switchLoading} languageModeSwitch={this.languageModeSwitch} specialModeSwitch={this.specialModeSwitch} />
      );
    }
}
export default App;
const RenderData= ({specialMode,language,languageModeSwitch, specialModeSwitch,switchLoading}) => {
    return (
        <div>
            <Helmet>
            <meta name="werwer" content={"1234567"} />

            </Helmet>
                <BrowserRouter>
                    <Header 
                        specialModeSwitch={specialModeSwitch}
                        specialMode={specialMode}
                        language={language}
                        languageModeSwitch={languageModeSwitch}
                    />
                    <Container specialMode={specialMode} >
                    <Switch>
                        
                        <Route
                            onUpdate={() => {
                                language=false
                                this.setState({})
                                window.scrollTo(0, 0)}}
                            exact path={"/"}
                            component={
                                ()=><Main switchLoading={switchLoading}  language={language} languageModeSwitch={languageModeSwitch} />
                            }/>
                             <Route
                            onUpdate={
                                () => {
                                    language=true
                                this.setState({})
                                    window.scrollTo(0, 0)}}
                            exact path={"/en"}
                            component={
                                ()=><ENMain switchLoading={switchLoading}    language={language} languageModeSwitch={languageModeSwitch} key={Math.random(1000)}  />
                            }
                        />
                        <Route
                            exact path={"/en/new_page/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><ENNewsPage {...props} switchLoading={switchLoading}   language={language} key={Math.random(1000)} />
                            }
                        />
                        <Route
                            exact path={"/new_page/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><NewsPage {...props} switchLoading={switchLoading}   language={language} key={Math.random(1000)} />
                            }
                        />
                        <Route
                            exact path={"/en/menu/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            render={
                                (props) => <ENMainMenuContentPage props={props} {...props} switchLoading={switchLoading}  key={Math.random(1000)}/>
                            }
                        />
                        <Route
                            exact path={"/menu/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            render={
                                (props) => <MainMenuContentPage props={props} {...props} switchLoading={switchLoading}  key={Math.random(1000)}/>
                            }
                        />
                        <Route
                            exact path={"/актуально/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><SideMenuContentPage switchLoading={switchLoading}  language={language} {...props}/>
                            }
                        />
                        <Route
                            exact path={"/search/:search"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><SearchedPage switchLoading={switchLoading}  language={language} {...props} key={Math.random(1000)}/>
                            }
                        />
                        <Route
                            exact path={"/en/All_news/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><ENAllNewsPageParams switchLoading={switchLoading}  language={language} {...props} key={Math.random(1000)}/>
                            }
                        />
                        <Route
                            exact path={"/All_news/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><AllNewsPageParams switchLoading={switchLoading}  language={language} {...props} key={Math.random(1000)}/>
                            }
                        />
                        
                        
                        <Route
                            exact path={"/en/history_war/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><ENHistoryPage switchLoading={switchLoading}  language={language} key={Math.random(1000)} {...props}/>
                            }
                        />
                        <Route
                            exact path={"/history_war/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><HistoryOfWar switchLoading={switchLoading}  language={language} key={Math.random(1000)} {...props}/>
                            }
                        />
                        <Route
                            exact path={"/en/year/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><ENHistoryWarPage switchLoading={switchLoading} language={language} key={Math.random(1000)} {...props}/>
                            }
                        />
                        <Route
                            exact path={"/year/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><HistoryOfWaPage switchLoading={switchLoading} language={language} key={Math.random(1000)} {...props}/>
                            }
                        />
                        <Route
                            exact path={"/Нормативно-правова база"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><RegulatoryFramework switchLoading={switchLoading}  language={language} key={Math.random(1000)} {...props}/>
                            }
                        />
                        <Route
                            exact path={"/files/*"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><DownloadPage switchLoading={switchLoading}  language={language} key={Math.random(1000)} {...props}/>
                            }
                        />
                        <Route
                            exact path={"/Корисні посилання"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><UsefulLinksPage switchLoading={switchLoading}  language={language} key={Math.random(1000)} {...props}/>
                            }
                        />
                        <Route
                            exact path={"/en/photo/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><ENFotoPage switchLoading={switchLoading}  language={language} {...props}/>
                            }
                        />
                        <Route
                            exact path={"/photo/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><FotoPage switchLoading={switchLoading}  language={language} {...props}/>
                            }
                        />
                        <Route
                            exact path={"/en/videos/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><ENVideoPage switchLoading={switchLoading}  language={language} {...props}/>
                            }
                        />
                        <Route
                            exact path={"/videos/:id"}
                            onUpdate={() => window.scrollTo(0, 0)}
                            component={
                                (props)=><VideoPage switchLoading={switchLoading}  language={language} {...props}/>
                            }
                        />
                        <Route
                            exact path={"/minimized"}
                        />
                        <Route
                            exact path={"/mapsite"}
                            onUpdate={() => window.scrollTo(0, 0)} component={(props)=><MapSitePage {...props} />}/>
                       <Route
                            exact path={"/en/mapsite"}
                            onUpdate={() => window.scrollTo(0, 0)} component={(props)=><MapSitePage {...props}  key={Math.random(1000)} />}/>
                      
                        <Route
                            exact path={"/Новина/test/zsu_site/backeand/site/v_2/2020/3123/id/news/seen/:id"}
                            onUpdate={() => window.scrollTo(0, 0)} component={(props)=><NewsPage {...props} switchLoading={switchLoading}   language={language} key={Math.random(1000)} />}/>
                        <Route
                            path={"/*"}
                            component={NotFoundPage}
                        /> {/*if Not found page*/}
                    </Switch>
                    </Container>
                    <Footer language={language}/>

                </BrowserRouter>
            
        </div>
    )
}
const Logos =styled.img`
@media only screen and (min-width: 1400px){
    height:140px;
  }
 @media only screen and (min-width: 1200px) and (max-width: 1399px){
    height:120px;
  }
 @media only screen and (min-width: 1024px) and (max-width: 1199px){
    height:102px;
  }
  @media only screen and (min-width: 880px)and (max-width: 1023px){
    height:88px;
  }
  @media only screen and (min-width: 640px) and (max-width: 879px){
    height:88px;
  }
  @media only screen and (min-width: 480px) and (max-width: 639px){
      height:88px;
  }
  @media only screen and (max-width: 479px){
      height:88px;
  }
`;
const Spenner=styled.div`
text-align: center;
color: #000000;
padding: 32px;
@media only screen and (min-width: 1400px){
  font-size: 34px;
}
 @media only screen and (min-width: 1200px) and (max-width: 1399px){
  font-size: 34px;
  }
 @media only screen and (min-width: 1024px) and (max-width: 1199px){
  font-size: 24px;
  }
  @media only screen and (min-width: 880px)and (max-width: 1023px){
    font-size: 24px;
  }
  @media only screen and (min-width: 640px) and (max-width: 879px){
    font-size: 24px;
  }
  @media only screen and (min-width: 480px) and (max-width: 639px){
    font-size: 24px;
  }
  @media only screen and (max-width: 479px){
    font-size: 18px;
  }
`
const Container = styled.div`
margin-top:152px;
background: #fff;
    ${props => props.specialMode ? "filter: grayscale(1)" : ""};
                   @media only screen and (max-width: 1023px)
 {
    margin-top: 60px;
}
`;
