import styled from "styled-components";

const Item=styled.div`
padding:16px;
filter: drop-shadow(0px 2px 4px rgba(32, 35, 38, 0.15));
    background:#FDFDFD;
border-radius: 2px;
display:grid;
&:hover{
    box-shadow: 0 4px 12px rgb(0 0 0 / 25%);
}
grid-template: "q w e" 104px
               "t t t" 104px / 1fr 1fr 88px;
               @media only screen and (min-width: 1400px) {
                height:320px;
                
                }
                @media only screen and (min-width: 1200px)and (max-width: 1399px){
                height:320px;
                
                }
                @media only screen and (min-width: 1024px) and (max-width: 1199px){
                height:240px;
                
                }
                @media only screen and (min-width: 880px) and (max-width: 1023px){
                    height:240px;
                
                
                }
                @media only screen and (min-width: 640px) and (max-width: 879px){
                    height:240px;
                    grid-template: "q w e" 104px
                    "t t t" 104px / 1fr 1fr 68px;
                }
                @media only screen and (min-width: 480px)and (max-width: 639px){
                    grid-template: "q w e" 104px
                    "t t t" 104px / 1fr 1fr 68px;
                    height: 230px;
                }
@media only screen and (max-width: 479px){
    grid-template: "q w e" 104px
    "t t t" 104px / 1fr 1fr 68px;
}

`;
export default Item;
