import React, {Component, Fragment} from "react";
import styled from "styled-components";
import {Link} from "react-router-dom";
import "../../fonts/mainStyle.css"
import {getNews} from"../../api/queries";
import {generateNewsReceivedAction} from "../../Storage/NewsStorage/actions";
import {generateCategoryReceivedAction} from "../../Storage/CategoryStorage/actions";
import { withRouter } from 'react-router-dom'
import {connect} from "react-redux";
import Arroy from "../../imgs/arroy_left.svg"
import OL from "../../PublicComponents/styled/Ol"
import LI from "../../PublicComponents/styled/LI"
import GhZsu from "../../imgs/ghzsy.png"
import Calendar from 'react-calendar'
import './index.css';
import UA from "date-fns/locale/ru"; // the locale you want
class AllNewsPage extends Component{
    componentDidMount() {
        
       
        getNews(`${this.props.language?"en":"ua"}`).then(news => {
            var input = document.getElementById("input_id").value;
            this.props.setCategoryToStore(news.category)

            this.props.setNewsToStore(news.news);
        });
    }
    getMonth(date){
        if(date.getMonth()+1===1){
            return this.props.language?"january":"січень"
        }if(date.getMonth()+1===2){
            return this.props.language?"february":"лютий"
            
        }if(date.getMonth()+1===3){
            return (this.props.language?"marth":"березня")
            
        }if(date.getMonth()+1===4){
            return this.props.language?"april":"квітень"
            
        }if(date.getMonth()+1===5){
            return this.props.language?"may":"травень"
            
        }if(date.getMonth()+1===6){
            return this.props.language?"june":"червень"
            
        }if(date.getMonth()+1===7){
            return this.props.language?"july":"липень"
            
        }if(date.getMonth()+1===8){
            return this.props.language?"august":"серпень"
            
        }if(date.getMonth()+1===9){
            return this.props.language?"september":"вересень"
            
        }if(date.getMonth()+1===10){
            return this.props.language?"october":"жовтень"
            
        }if(date.getMonth()+1===11){
            return this.props.language?"november":"листопад"
            
        }if(date.getMonth()+1===12){
            return this.props.language?"december":"грудень"
            
        }
        return null;

    }
    renderNewsNow(news){
        if(news.length!=0)
        return <div>
            <TittleRen >{(this.props.language?"Today":"Сьогодні")}</TittleRen>
                        {news.map((el)=>{
                 let today=false;
                 let date = new Date(el.date);
                      let nowDate = new Date ;
                 if(nowDate.getDate() === date.getDate() && nowDate.getMonth() === date.getMonth() )
                 {
                     today=true;
                 }
                 else{
         
                 }

                return (
                    <NewsItemWrapper>
                    <NewsItemsContainer>
                        <div>
                            <DateItem>{(this.props.language?"Today":"Сьогодні")} / {((date.getHours())<10)?('0'+date.getHours()):(date.getHours())}:{(date.getMinutes()<10)?('0'+date.getMinutes()):(date.getMinutes())}</DateItem>
                        </div>
                        <div>
                            <Link style={{textDecoration:"none"}}  to={{pathname:`/new_page/${el._id}`,
                        state:{
                            sub_el:news.slice(0,3)
                        }    
                            }}>  <TitleItem>{el.page_title} </TitleItem></Link>
                        </div>

                    </NewsItemsContainer>
                    {el.photo ?<NewsImg src={`data:image/png;base64,${el.photo.middle}`}></NewsImg>:<NoImge src={GhZsu}></NoImge>}
                </NewsItemWrapper>
                )
            })}
        </div>
    }
    renderNewsYesterday(news){
        
        if(news.length!=0)
        return <div>
            <TittleRen >{(this.props.language?"Yesterday":"Вчора")}</TittleRen>
        {news.map((el)=>{
             let date = new Date(el.date);
            return (
                <NewsItemWrapper>
                <NewsItemsContainer>
                    <div>
                        <DateItem>{(this.props.language?"Yesterday":"Вчора")} 
                         / {((date.getHours())<10)?('0'+date.getHours()):(date.getHours())}:{(date.getMinutes()<10)?('0'+date.getMinutes()):(date.getMinutes())}</DateItem>
                    </div>
                    <div>
                        <Link style={{textDecoration:"none"}}  to={{pathname:`/new_page/${el._id}`,
                        state:{
                            sub_el:news.slice(0,3)
                        }    
                            }}>  <TitleItem>{el.page_title} </TitleItem></Link>
                    </div>

                </NewsItemsContainer>
                {el.photo ?<NewsImg src={`data:image/png;base64,${el.photo.middle}`}></NewsImg>:<NoImge src={GhZsu}></NoImge>}
            </NewsItemWrapper>
            )
        })}
    </div>
    }
    renderNews(news){
       
        const sortedActivities = news.sort((a, b) => new Date(a.date) - new Date(b.date)).reverse()
        return (
        <div>
                        <TittleRen >{(this.props.language?"Previously":"Раніше")}</TittleRen>
        {sortedActivities.map(el => {
            let today=false;
            let date = new Date(el.date);
                 let nowDate = new Date ;
            if(nowDate.getDate() === date.getDate() && nowDate.getMonth() === date.getMonth() )
            {
                today=true;
            }
            else{
    
            }
            return (
                <NewsItemWrapper>
                    <NewsItemsContainer>
                        <div>
                            <DateItem>{((date.getUTCDate())<10?('0'+date.getUTCDate()):(date.getUTCDate()))} {" "}
                            {this.getMonth(date)} / {((date.getHours())<10)?('0'+date.getHours()):(date.getHours())}:{(date.getMinutes()<10)?('0'+date.getMinutes()):(date.getMinutes())}</DateItem>
                        </div>
                        <div>
                            <Link style={{textDecoration:"none"}}  to={{pathname:`/new_page/${el._id}`,
                        state:{
                            sub_el:news.slice(0,3)
                        }    
                            }}>  <TitleItem>{el.page_title} </TitleItem></Link>
                        </div>

                    </NewsItemsContainer>
                    {el.photo ?<NewsImg src={`data:image/png;base64,${el.photo.middle}`}></NewsImg>:<NoImge src={GhZsu}></NoImge>}
                </NewsItemWrapper>
            )
        })}
        </div>)
    }
    state = {
        value: new Date(),
      }
    
      onChange (value){
          this.setState({ value })
        }
    render() {
        const Button = withRouter(({ history }) => (
            <Calendar style={{paddingLeft:"32px",marginLeft:"10%"}}
              type='button'
              onChange={(el) => { history.push(`/All_news/${this.props.language?"en":"ua"}`) }}
            />
          ))
        const { value } = this.state;
        const sortedActivities = this.props.news
        const category = this.props.category

        var body = document.body;
        body.scroll(0.0,0.0)
        return (
            <div style={{background:"#FDFDFD",paddingBottom:"30px"}}>
               <Div>
                <OL style={{gridArea:"a"}}>
                <li>
                <Link style={{textDecoration:"none",color:"#000000", fontSize:"16px"}} to="/">{this.props.language?"Main":"Головна"}</Link>
                <img height={"12px"} width={"7px"} style={{margin:"0px 12px 0px 12px"}} src={Arroy}/>
                </li>
             
                <LI style={{color:"#909F84"}}>
                    <Link style={{textDecoration:"none", color:"#909F84", fontSize:"16px"}} to={`/All_news`}>{this.props.language?"News":"Новини"}</Link>
                </LI>
                
                </OL>
                </Div>
                
            <Container>
                <BlockCont>
                    <Card>
                        <TiTle>
                        Категорії
                        </TiTle>
                      {category.map(el=>{
                          if((this.props.language?el.title_en:el.title_ua)===this.state.title)
                          return (               <SubEl  >
                            <Link  style ={{ color: "#000000",textDecoration:  "none",}} to={{pathname:`/All_news/${el._id}`,
                                state: {
                                    id: 123,
                                    title: `${this.props.language?el.title_en:el.title_ua}`,
                                    sub_el: []
                                  }
                                }}>
                                   {this.props.language?el.title_en:el.title_ua}
                                </Link>
                            </SubEl>)
                            else{
                                return(
                                    <SubEl  >
                            <Link  style ={{ color: "#000000",textDecoration:  "none",}} to={{pathname:`/All_news/${this.props.language?"en":"ua"}?category=${el._id}&date=`,
                                        state: {
                                            id: 123,
                                            title: `${this.props.language?el.title_en:el.title_ua}`,
                                                sub_el: []
                                          }
                                        }}>
                                            {this.props.language?el.title_en:el.title_ua}
                                        </Link>
                                    </SubEl>)
                            }

                      })}
                                                 
                                                               
                                                                   
                    </Card>
                    <BlockFilterByDate>
                    <Button ></Button>

                    </BlockFilterByDate>
                </BlockCont>
            <ContainerTitle style={{gridArea:"a",fontSize:"2em",  textAlign:"left",fontWeight:"700"}}>{this.props.language?"News":"Новини"}</ContainerTitle>
                <Dive>
                    <InfoContainer>
                        {this.renderNewsNow(sortedActivities.filter((el) =>{
                            let date =new Date(el.date);
                            let dateNow=new Date();
                           return date.getDate()===dateNow.getDate()&&date.getMonth()===dateNow.getUTCMonth()
                        }))}
                        {this.renderNewsYesterday(sortedActivities.filter((el) =>{
                            let date =new Date(el.date);
                            let dateNow=new Date();
                           return date.getDate()===dateNow.getDate()-1&&date.getMonth()===dateNow.getUTCMonth()
                        }))}
                        {this.renderNews(sortedActivities.filter((el) =>{
                            let date =new Date(el.date);
                            let dateNow=new Date();
                           return (date.getDate()!=dateNow.getDate()&&date.getMonth()!=dateNow.getUTCDate()-1)
                           
                                    
                        }))}
                    </InfoContainer>

                </Dive>
            </Container>
            </div>
        );
    }
}

let mapStateToProps = state => {
    return {
        news: state.newsReducer.news,
        category: state.categoryReducer.category
    }
};

let mapDispatchToProps = dispatch => {
    return {
        setNewsToStore: news => dispatch(generateNewsReceivedAction(news)),
        setCategoryToStore:category=>dispatch(generateCategoryReceivedAction(category))
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(AllNewsPage);

