import React, {Component} from "react";
import styled from "styled-components";
import {MAIN_URL} from "../../api/URLs";
import "./index.css";
import "../../fonts/mainStyle.css"
import Item from "../../PublicComponents/styled/Item"
import Text from "../../PublicComponents/styled/Text"
import Img from "../../PublicComponents/styled/Img"
import {Link} from "react-router-dom";

class Commands extends Component{
    constructor(props){
        super(props);
        this.renderCommands = this.renderCommands.bind(this);
    }

    // renderCommands(commands){
    //     return  commands.map(el => {
    //         return (
    //         <Item quantity={commands.length} href={`${el.link}` } target="_blank" >
    //             <Img   style={{
    //                 backgroundImage: `url("data:image/png;base64,${el.back_photo}")`
    //             }}>
    //                 <div className="Cont">
    //                 <div  className='ForcesText'>{this.props.language?el.title_en:el.title_ua}
    //                 </div>
    //                     <div className="Wrap">
    //                 <img className="EmblemaForces" src={`data:image/png;base64,${el.front_photo}`}/>
    //                     </div>
    //                     </div>
    //             </Img>
    //
    //         </Item>
    //         )
    //     })
    // }
    renderCommands(forces){
        return forces.map(el => {
                return (
                    <LI style={{textDecoration:"none",listStyle:"none"}} >
                    <Item>
                        <A href={el.link}  target="_blank" style={{textDecoration:"none",listStyle:"none"}}>
                            <Span>
                                <Img src={`https://www.zsu.gov.ua/image/${el.signImg}`}/>
                            </Span>
                            <Text>
                                {this.props.language?el.title_en:el.title_ua}
                            </Text>
                        </A>
                        
                   
                    </Item>
                    </LI>

                )
            }
        )
    }
    render() {
        return(
            <MainContainer >
                <ContainerTitle>{this.props.language?"Command, types of troops (forces), ":"Командування"}</ContainerTitle>
                <ContainerPod>
                    {this.props.language?"the Armed Forces of Ukraine":"Збройних Сил України"}
                </ContainerPod>
                <Container>

                {this.renderCommands(this.props.commands)}
            </Container>
            </MainContainer>
        )
    }
}

export default Commands;
const LI =styled.li`
&:hover{
    box-shadow: 0 2px 4px rgb(0 0 0 / 15%);
}
`
const A = styled.a`
display: inline-block;
width: 100%;
padding: 14px;
min-height: 186px;

    -webkit-box-shadow: 0 2px 4px rgb(0 0 0 / 15%);
    box-shadow: 0 2px 4px rgb(0 0 0 / 15%);
    -webkit-transition: -webkit-box-shadow 300ms;
    -o-transition: box-shadow 300ms;
    transition: box-shadow 300ms;
    transition: box-shadow 300ms,-webkit-box-shadow 300ms;
    @media only screen and (max-width: 599px)
{
    height: 193px;
}
`
const Span = styled.span`
display: inline-block;
    height: 64px;
    width: 64px;
    margin-bottom: 16px;
    @media only screen and (max-width: 599px)
 {
    height: 44px;
    margin-bottom: 7px;
}
`;
const Container=styled.ul`

@media only screen and (max-width: 1023px)
 {
    grid-template-columns: repeat(4,1fr);
}


@media only screen and (max-width: 850px)
 {
    grid-template-columns: repeat(3,1fr);
}
@media only screen and (max-width: 599px)
 {
    grid-template-columns: repeat(2,1fr);
}
    display: grid;
    grid-template-columns: repeat(5,1fr);
    gap: 16px;
    list-style-type: none;

`;

const ContainerTitle = styled.div`
    font-weight: 600;

@media only screen and (max-width: 1020px) {
    font-size: 20px;
    line-height: 26px;
}
    font-size: 24px;
    line-height: 32px;
    color: #202020;


`;
const ContainerPod = styled.div`
    margin-bottom: 32px;
    color: #656565;
    padding: 0;
    border: 0;
    vertical-align: baseline;
`;

const MainContainer=styled.div`
color:#000000
grid-area:f;
@media only screen and (max-width: 1020px)
 {
    padding-left: 16px;
    padding-right: 16px;
}
    max-width: 1400px;
    margin: 0 auto;
    padding-left: 32px;
    padding-right: 32px;
`;
