import styled from "styled-components";
import { getNewsById } from "../../api/queries";
import { generateNewReceivedAction } from "../../Storage/NewStorage/actions";
import { connect } from "react-redux";
import { FaArrowCircleUp } from "react-icons/fa";
import { FaRegSave, FaArrowAltCircleLeft, FaArrowAltCircleRight } from "react-icons/fa";
import Arroy from "../../imgs/arroy_left.svg"
import OL from "../../PublicComponents/styled/Ol"
import LI from "../../PublicComponents/styled/LI"
import MyComponent from 'react-fullpage-custom-loader'
import Logo from "../../imgs/logo.png"
import GhZsu from "../../imgs/ghzsy.png"
import { withRouter } from 'react-router-dom'
import { Helmet } from "react-helmet";
import TittleRen from "../../PublicComponents/styled/TittleRen"
import { getNewsUa, getSideLinksUa, getForces, getCommands } from "../../api/queries";
import { ADIS, AACT } from "../../PublicComponents/styled/A"
import Slider from "react-slick";
import "./index.css"
import FullScreenSlider from "./fullscreen_slider"
import ArroyLeft from "../../imgs/left-arrow.png"
import ArroyRight from "../../imgs/right-arrow.png"

function SampleNextArrow(props) {
    const { className, style, onClick } = props;
    return (
        <div
            className={className}
            style={{ ...style, display: "block" }}
            onClick={onClick}
        >
            <img width="30px" src={ArroyRight}/>

        </div>
    );
}

function SamplePrevArrow(props) {
    const { className, style, onClick } = props;
    return (
        <div
            className={className}
            style={{ ...style, display: "block" }}
            onClick={onClick}
        >
            <img width="30px" src={ArroyLeft}/>

        </div>
    );
}
class NewsPage extends Component {

    getMonth(date) {
        if (date.getMonth() + 1 === 1) {
            return window.location.pathname.indexOf("/en") >= 0 ? "january" : "січня"
        } if (date.getMonth() + 1 === 2) {
            return window.location.pathname.indexOf("/en") >= 0 ? "february" : "лютого"

        } if (date.getMonth() + 1 === 3) {
            return (window.location.pathname.indexOf("/en") >= 0 ? "marth" : "березня")

        } if (date.getMonth() + 1 === 4) {
            return window.location.pathname.indexOf("/en") >= 0 ? "april" : "квітня"

        } if (date.getMonth() + 1 === 5) {
            return window.location.pathname.indexOf("/en") >= 0 ? "may" : "травня"

        } if (date.getMonth() + 1 === 6) {
            return window.location.pathname.indexOf("/en") >= 0 ? "june" : "червня"

        } if (date.getMonth() + 1 === 7) {
            return window.location.pathname.indexOf("/en") >= 0 ? "july" : "липня"

        } if (date.getMonth() + 1 === 8) {
            return window.location.pathname.indexOf("/en") >= 0 ? "august" : "серпня"

        } if (date.getMonth() + 1 === 9) {
            return window.location.pathname.indexOf("/en") >= 0 ? "september" : "вересня"

        } if (date.getMonth() + 1 === 10) {
            return window.location.pathname.indexOf("/en") >= 0 ? "october" : "жовтня"

        } if (date.getMonth() + 1 === 11) {
            return window.location.pathname.indexOf("/en") >= 0 ? "november" : "листопада"

        } if (date.getMonth() + 1 === 12) {
            return window.location.pathname.indexOf("/en") >= 0 ? "december" : "грудня"

        }
        return null;

    }

    constructor(props) {
        super(props);

        this.state = {
            content: "",
            title: "",
            loading: true,
            currentSlide: 0,
            image: [],
            show: false,
            sub_el: [],
            nav1: null,
            nav2: null

        };
        this.toggleShow = this.toggleShow.bind(this);

    }

    componentDidMount() {
        this.setState({
            headerTop: document.getElementById('header'),
            nav1: this.slider1,
            nav2: this.slider2
        })
        getNewsUa(0, 10, "", "").then(news => {
            this.setState(
                {
                    sub_el: news.news.filter(el => el._id !== this.props.match.params.id).slice(0, 3)
                });
        })

        getNewsById(this.props.match.params.id).then(news => {
            this.props.setNewToStore(news)

            this.setState(
                {
                    loading: false,
                });
        })
        window.addEventListener('wheel', this.handleScroll);
        var body = document.body;
        body.scroll(0.0, 0.0)
    }
    toggleShow() {
        this.setState({ show: !this.state.show });
        document.body.scroll(0.0, 0.0)
        var body = document.body;
        body.scroll(0.0, 0.0)
        var body = window.document.getElementById('root')
        var x = window.innerWidth || body.clientWidth;
        if (x > 1024) {
            this.state.headerTop.style.height = "100px"

        } else {
            this.state.headerTop.style.height = "60px"
        }
    }
    handleShowDialog = (image, index) => {
        this.setState({ isOpen: !this.state.isOpen, image: index });
    };
    renderNewsCard(images) {
        return images.map(el => {
            return (
                <div>
                    <img width="100%" onClick={() => {
                        this.handleShowDialog(el, images.indexOf(el))
                    }} src={`/image/${el}`} />
                </div>
            )
        });
    }
    newrenderNewsCard(images) {
        return images.map(el => {
            return (
                <div>
                    <IMG onClick={() => {
                        this.handleShowDialog(el, images.indexOf(el))
                    }} src={`/image/${el}`} />
                </div>
            )
        });
    }
    renderSlider(images, state) {
        const testSettings = {
            opacity: "1",
            outline: '0'
        }
        const testSettings2 = {
            opacity: "0.5",
            outline: '0'
        }
        const settings = {

            nextArrow: <SampleNextArrow />,
            prevArrow: <SamplePrevArrow />,
            beforeChange: (prev, next) => {
                this.setState({ currentSlide: next });
            },

            arrows: true,
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,

        };

        return (
            <Conta>
                <Slider {...settings} >

                    {this.newrenderNewsCard(images)}
                </Slider>

            </Conta>
        )
    }
    render() {
        const settings = {

            nextArrow: <SampleNextArrow />,
            prevArrow: <SamplePrevArrow />,
            beforeChange: (prev, next) => {
                this.setState({ currentSlide: next });
            },

            arrows: true,
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,

        };

        if (this.state.loading) {
            return (
                <MyComponent wrapperBackgroundColor={"#ececec"}
                    sentences={[""]}
                    customLoader={
                        <Spenner>
                            <Logos src={Logo} />
                            <div>
                                Збройні Сили України
                            </div>
                        </Spenner>
                    }
                />
            )
        }
        let today = false;
        let date = new Date(this.props.one_new.publishDate);
        let nowDate = new Date;
        if (nowDate.getDate() === date.getDate() && nowDate.getMonth() === date.getMonth()) {
            today = true;
        }
        else {
        }
        return (
            <MAINContainer style={{ background: "#FDFDFD", paddingBottom: "30px", textAlign: "left!important" }}>
                <Helmet>
                    <title>{this.props.one_new.title} </title>
                    <meta name="description" content={this.props.one_new.description} />
                    <meta name="keywords" content={this.props.one_new.title} />
                    <meta name="title" content={this.props.one_new.title} />
                    <meta property="og:type" content="article" />
                    <meta property="og:title" content={this.props.one_new.title} />
                    <meta property="og:description" content={this.props.one_new.title} />
                    <meta property="og:image:url" content="https://www.zsu.gov.ua/image/%D0%92%D0%9C%D0%A1%D0%92%D0%9C%D0%A1-1804515691.jpg" />
                </Helmet>
                <metadata name="description" content={this.props.one_new.title}></metadata>
               
                {this.state.isOpen && (
                    <FullScreenSlider slider={this.props.one_new.slider} handleShowDialog={this.handleShowDialog} image={this.state.image}></FullScreenSlider>
                    
                )}
                <OL >
                    <li style={{ display: "flex" }}>
                        <ADIS to="/">{this.props.language ? "Main" : "Головна"}</ADIS>
                        <img height={"12px"} width={"7px"} style={{ margin: "6px 12px 6px 12px" }} src={Arroy} />

                    </li>

                    <LI style={{ display: "flex" }}>
                        <ADIS href={`/All_news/${this.props.language ? "en" : "ua"}`}>{this.props.language ? "News" : "Новини"}</ADIS>
                        <img height={"12px"} width={"7px"} style={{ margin: "6px 12px 6px 12px" }} src={Arroy} />

                    </LI>

                    <LI style={{ color: "#909F84", fontSize: "16px", display: "flex" }}>
                        <AACT >
                            {`${this.props.one_new.title}`}
                        </AACT>

                    </LI>
                </OL>


                <Container>
                    <ArcticleContent>
                        <H1 style={{ fontSize: "2em", textAlign: "left", fontWeight: "700", paddingBottom: "16px" }}>{this.props.one_new.title}</H1>

                        <DateItem>{today ? (this.props.language ? "Today" : "Сьогодні") : ((date.getUTCDate()) < 10 ? ('0' + date.getUTCDate()) : (date.getUTCDate()))}
                            {today ? ("") : "  " + this.getMonth(date)} {" "}/{" "}
                            {((date.getHours()) < 10) ? ('0' + date.getHours()) : (date.getHours())}:{(date.getMinutes() < 10) ? ('0' + date.getMinutes()) : (date.getMinutes())}
                            <span style={{ marginLeft: "40px" }}>
                                ID:{this.props.match.params.id}
                            </span>
                        </DateItem>
                        <Butoon onClick={this.toggleShow} opened={this.state.show}><FaArrowCircleUp size={"3em"} style={{ color: "#384038" }}>
                        </FaArrowCircleUp>
                        </Butoon>

                        <div style={{ textAlign: "left", alignItems: "left !important", marginBottom: "80px" }}>
                            {this.props.one_new.slider ? this.props.one_new.slider.length !== 0 ? this.renderSlider(this.props.one_new.slider, this.state) : <div></div> : <div></div>}

                            <Mains dangerouslySetInnerHTML={{ __html: this.props.one_new.html_body }} />

                            <div>
                                {this.props.one_new.files ? this.props.one_new.files.length != 0 ? <TittleRen style={{ marginTop: "25px" }}>{(window.location.pathname.indexOf("/en") >= 0 ? "Files" : " Файли ")}</TittleRen> : null : null}

                                {this.props.one_new.files ? this.props.one_new.files.map(el => {
                                    const SubEle = withRouter(({ history }) => (
                                        <a style={{ textDecoration: "none" }} href={`/download_file/${el}`} target="_blank">
                                            <NavBarDropdownItem>
                                                <img src={File} height={"24px"} width={"16px"} />
                                                <div style={{ paddingLeft: "10px" }}>{el}</div>
                                            </NavBarDropdownItem>
                                        </a>
                                    ))
                                    return (
                                        <SubEle></SubEle>
                                    )


                                }) : null}
                            </div>
                        </div>
                        <LatestNews>
                            <TittleRen >{(this.props.language ? "Read also" : "Читайте також")}</TittleRen>
                            {this.state.sub_el ? this.state.sub_el.map(el => {
                                let today = false;
                                let date = new Date(el.publishDate);
                                let nowDate = new Date;
                                if (nowDate.getDate() === date.getDate() && nowDate.getMonth() === date.getMonth()) {
                                    today = true;
                                }
                                else {

                                }
                                return (
                                    <NewsItemWrapper>
                                        <NewsItemsContainer>
                                            <NewsItemLink href={`/new_page/${el._id}`}>
                                                <NewsItemText>
                                                    <DateItem>{today ? (this.props.language ? "Today" : "Сьогодні") : ((date.getUTCDate()) < 10 ? ('0' + date.getUTCDate()) : (date.getUTCDate()))}
                                                        {today ? ("") : "  " + this.getMonth(date)} /
                                                        {((date.getHours()) < 10) ? ('0' + date.getHours()) : (date.getHours())}:{(date.getMinutes() < 10) ? ('0' + date.getMinutes()) : (date.getMinutes())}</DateItem>
                                                    <TitleItem>{el.title} </TitleItem>
                                                </NewsItemText>
                                                {el.mainPhoto ? <NewsImage><NewsImg src={`/image/${el.mainPhoto ?? ""}`}></NewsImg></NewsImage> : <div></div>}
                                            </NewsItemLink>


                                        </NewsItemsContainer>

                                    </NewsItemWrapper>
                                )
                            }) : null}
                        </LatestNews>

                    </ArcticleContent>
                </Container>
            </MAINContainer>
        )
    }


}

let mapStateToProps = state => {
    return {
        one_new: state.one_newReducer.one_new,
    }
};

let mapDispatchToProps = dispatch => {
    return {
        setNewToStore: one_new => dispatch(generateNewReceivedAction(one_new)),
    }
};
export default connect(mapStateToProps, mapDispatchToProps)(NewsPage);

