class ENMainNews extends Component{
   
    getMonth(date){
        if(date.getMonth()+1===1){
            return "january"
        }if(date.getMonth()+1===2){
            return "february"
            
        }if(date.getMonth()+1===3){
            return ("marth")
            
        }if(date.getMonth()+1===4){
            return "april"
            
        }if(date.getMonth()+1===5){
            return "may"
            
        }if(date.getMonth()+1===6){
            return "june"
            
        }if(date.getMonth()+1===7){
            return "july"
            
        }if(date.getMonth()+1===8){
            return "august"
            
        }if(date.getMonth()+1===9){
            return "september"
            
        }if(date.getMonth()+1===10){
            return "october"
            
        }if(date.getMonth()+1===11){
            return "november"
            
        }if(date.getMonth()+1===12){
            return "december"
            
        }
        return null;

    }
    
    renderMainNews(blocks,cat){
        let count=0;
        var  color = "#47523F"
        return blocks.map(el => {
            let date = new Date(el.publishDate);
            let nowDate = new Date() ;
            let today=false;
            if(nowDate.getDate() === date.getDate() && nowDate.getMonth() === date.getMonth() )
            {
                today=true;
            }
            else{

            }
            if(count%2===0){
                if(count===0){
                    
                        count+=1
                        return (
                           <NewsCardWide>
                               <NewsCardLink href={`/new_page/${el._id}`}>
                                   <NewsCardImg>
                                       <NewsCardImage src={el.mainPhoto ? `/image/${el.mainPhoto}` : NoImage}></NewsCardImage>
                                   </NewsCardImg>
                                   <NewsCardInner>
                                       <NewsCardContent>
                                            <NewsCardDate>
                                            {today?("Today"):((date.getUTCDate())<10?('0'+date.getUTCDate()):(date.getUTCDate()))} 
                                {today?(""):"  "+this.getMonth(date)} /
                                 {((date.getHours())<10)?('0'+date.getHours()):(date.getHours())}:{(date.getMinutes()<10)?('0'+date.getMinutes()):(date.getMinutes())}
                                            </NewsCardDate> 
                                            <NewsCardText>
                                            {el.title}
                                            </NewsCardText>    
                                       </NewsCardContent>
                                   </NewsCardInner>
                               </NewsCardLink>
                           </NewsCardWide>
                    )
                   }else{
                       
                        count+=1
                        return (
                            <NewsCardWide2 style={{border:"2px solid #47523f"}}>
                            <NewsCardLink2 href={`/new_page/${el._id}`}>
                                
                                <NewsCardInner2>
                                <NewsCardImg2>
                                <NewsCardImage2 src={(el.mainPhoto ? `/image/${el.mainPhoto}` : NoImage)}></NewsCardImage2>
                                </NewsCardImg2>
                                    <NewsCardContent2>
                                        <NewsCardDate2>
                                        {today?("Today"):((date.getUTCDate())<10?('0'+date.getUTCDate()):(date.getUTCDate()))} 
                            {today?(""):"  "+this.getMonth(date)} /
                            {((date.getHours())<10)?('0'+date.getHours()):(date.getHours())}:{(date.getMinutes()<10)?('0'+date.getMinutes()):(date.getMinutes())}
                                        </NewsCardDate2> 
                                        <NewsCardText2>
                                        {el.title}
                                        </NewsCardText2>    
                                    </NewsCardContent2>
                                </NewsCardInner2>
                            </NewsCardLink2>
                        </NewsCardWide2>
                            )
                   }
                
            }else{
                    count+=1
                    return (
                        <NewsCardWide2 style={{border:"2px solid #edd7a2"}}>
                        <NewsCardLink2 href={`/new_page/${el._id}`}>
                            
                            <NewsCardInner2>
                            <NewsCardImg2>
                                <NewsCardImage2 src={(el.mainPhoto ? `/image/${el.mainPhoto}` : NoImage)}></NewsCardImage2>
                            </NewsCardImg2>
                                <NewsCardContent2>
                                     <NewsCardDate2>
                                     {today?("Today"):((date.getUTCDate())<10?('0'+date.getUTCDate()):(date.getUTCDate()))} 
                         {today?(""):"  "+this.getMonth(date)} /
                          {((date.getHours())<10)?('0'+date.getHours()):(date.getHours())}:{(date.getMinutes()<10)?('0'+date.getMinutes()):(date.getMinutes())}
                                     </NewsCardDate2> 
                                     <NewsCardText2>
                                     {el.title}
                                     </NewsCardText2>    
                                </NewsCardContent2>
                            </NewsCardInner2>
                        </NewsCardLink2>
                    </NewsCardWide2>
                )
               }
        })
         }
    render() {

       
        return(
            <MainContainer>
                <ContainerTitle>{"Main news"}</ContainerTitle>
                <ContainerForSuggested2>
                {
                this.renderMainNews(this.props.blocks,)}
                </ContainerForSuggested2>


                
            </MainContainer>
        )
    }
}
