import React, {Component} from "react";
import styled from "styled-components";
import {Link} from "react-router-dom";

class UsefulLinks extends Component{
    constructor(props){
        super(props);

    }

    renderLinks(links){
        return links.map(el => {
            return <LI>
                <A href={el.link}  target="_blank">
                {window.location.pathname.indexOf("/en")>=0?el.title_en:el.title_ua}
            </A>
            </LI> 
        })
    }

    render(){
        return (
            
                this.renderLinks(this.props.links)
           
        )
    }
}

export default UsefulLinks;

const Wrapper = styled.div`  
     
`;

const Title = styled.div`
    margin-top:10px;
    margin-bottom:10px;

    cursor: default;
    font-size: 16px;
    color: #EDD7A2;
    @media only screen  and (max-width: 879px){
        font-size: 12px;
        margin:auto;
       }
`;
const LI =styled.li`
margin-bottom: 16px;
`
const A=styled.a`
text-decoration:none;

font-size: 16px;
    line-height: 20px;
    letter-spacing: .1px;
    font-weight: 400;
    color: #edd7a2;
    cursor:pointer;
`
