let mapStateToProps = state => {
    return {
        sideLinks: state.navSideLinksReducer.sideLinks,
        news: state.newsReducer.news,
        category: state.categoryReducer.category,
        forces:state.forcesReducer.forces,
        commands:state.commandsReducer.commands,
        socialLinks: state.socialLinksReducer.socialLinks,
        
    }
};

let mapDispatchToProps = dispatch => {
    return {
        setSideMenuLinksToStore: sideLinks => dispatch(generateSideLinksAction(sideLinks)),
        setNewsToStore: news => dispatch(generateNewsReceivedAction(news)),
        setCategoryToStore:category=>dispatch(generateCategoryReceivedAction(category)),
        setForcesToStore: forces => dispatch(generateForcesReceivedAction(forces)),
        setCommandsToStore: commands => dispatch(generateCommandsReceivedAction(commands)),

    }
};

export default connect(mapStateToProps, mapDispatchToProps)(ENMain);
