import {generateSideLinksAction} from "../../Storage/NavSideLinksStorage/actions";
import {generateNewsReceivedAction} from "../../Storage/NewsStorage/actions";
import {generateForcesReceivedAction} from "../../Storage/ForcesStorage/actions";
import {connect} from "react-redux";
import {MAIN_URL} from "../../api/URLs";
import Arroy from "../../imgs/arroy_left.svg"
import OL from "../../PublicComponents/styled/Ol"
import LI from "../../PublicComponents/styled/LI"
