import MyComponent from 'react-fullpage-custom-loader'
import Logo from "../../imgs/logo.png"
class SearchedPage extends Component{
    constructor(props){
        super(props);
        this.state = {
            
            loading: true,
          };
    }
    getMonth(date){
        if(date.getMonth()+1===1){
            return this.props.language?"january":"січня"
        }if(date.getMonth()+1===2){
            return this.props.language?"february":"лютого"
            
        }if(date.getMonth()+1===3){
            return (this.props.language?"marth":"березня")
            
        }if(date.getMonth()+1===4){
            return this.props.language?"april":"квітня"
            
        }if(date.getMonth()+1===5){
            return this.props.language?"may":"травня"
            
        }if(date.getMonth()+1===6){
            return this.props.language?"june":"червня"
            
        }if(date.getMonth()+1===7){
            return this.props.language?"july":"липня"
            
        }if(date.getMonth()+1===8){
            return this.props.language?"august":"серпня"
            
        }if(date.getMonth()+1===9){
            return this.props.language?"september":"вересня"
            
        }if(date.getMonth()+1===10){
            return this.props.language?"october":"жовтня"
            
        }if(date.getMonth()+1===11){
            return this.props.language?"november":"листопада"
            
        }if(date.getMonth()+1===12){
            return this.props.language?"december":"грудня"
            
        }
        return null;

    }
   componentDidMount() {

        this.props.language?getNewsEn(0,25,"","").then((news)=>{
                this.props.setNewsToStore(
                    news.news
                )

            }):
            getNewsUa(25).then((news) => {
            this.props.setNewsToStore(
                news.news
            );
        });
       
    }

    renderNews(news){
        
        const sortedActivities = news.sort((a, b) => new Date(a.date) - new Date(b.date)).reverse()
        return (
        <div>
                        <TittleRen >{(this.props.language?"News":"Новини")}</TittleRen>
        {sortedActivities.map(el => {
            let today=false;
            let date = new Date(el.publishDate);
                 let nowDate = new Date ;
            if(nowDate.getDate() === date.getDate() && nowDate.getMonth() === date.getMonth() )
            {
                today=true;
            }
            else{
    
            }
            return (
                <NewsItemWrapper>
                    <NewsItemsContainer>
                        <div>
                            <DateItem>{((date.getUTCDate())<10?('0'+date.getUTCDate()):(date.getUTCDate()))} {" "}
                            {this.getMonth(date)} / {((date.getHours())<10)?('0'+date.getHours()):(date.getHours())}:{(date.getMinutes()<10)?('0'+date.getMinutes()):(date.getMinutes())}</DateItem>
                        </div>
                        <div>
                            <Link style={{textDecoration:"none"}}  to={{pathname:`/new_page/${el._id}`,
                        state:{
                            sub_el:this.props.news.filter(ele=>ele._id!=el._id).slice(0,3)
                        }    
                            }}>  <TitleItem>{el.title} </TitleItem></Link>
                        </div>

                    </NewsItemsContainer>
                    {el.mainPhoto ?<NewsImg src={`/image/${el.mainPhoto}`}></NewsImg>:<NoImge src={GhZsu}></NoImge>}
                </NewsItemWrapper>
            )
        })}
        </div>)
    }
    
    render() {
        const query  = this.props.match.params.search;

        return (
            <div style={{background:"#FDFDFD",paddingBottom:"30px"}}>
            <Div>
             <OL style={{gridArea:"a"}}>
             <li>
             <Link style={{textDecoration:"none",color:"#000000", fontSize:"16px"}} to="/">{this.props.language?"Main":"Головна"}</Link>
             <img height={"12px"} width={"7px"} style={{margin:"0px 12px 0px 12px"}} src={Arroy}/>
             </li>
          
             <LI style={{color:"#909F84"}}>
                 <Link style={{textDecoration:"none", color:"#909F84", fontSize:"16px"}} >{this.props.language?"Search":"Пошук"}</Link>
             </LI>
             
             </OL>
             </Div>
             
         <Container>
             
         <ContainerTitle style={{gridArea:"a",fontSize:"2em",  textAlign:"left",fontWeight:"700",textAlign:"start"}}>{this.props.language?"Query result":"Результат пошуку"}</ContainerTitle>   
             <Dive>
                 <InfoContainer>
                     {this.renderNews(this.props.news.filter((el) => {
                        if (
                            el.description.toLowerCase().includes(query.toLowerCase()) ||
                            el.title.toLowerCase().includes(query.toLowerCase())
                        )
                            return el;
                    }))}
                     
                 </InfoContainer>

             </Dive>
         </Container>
         </div>
        );
    }
}

let mapStateToProps = state => {
    return {
        news: state.newsReducer.news,
        sideLinks: state.navSideLinksReducer.sideLinks,
    }
};

let mapDispatchToProps = dispatch => {
    return {
        setNewsToStore: news => dispatch(generateNewsReceivedAction(news)),
        setSideMenuLinksToStore: sideLinks => dispatch(generateSideLinksAction(sideLinks)),

    }
};

export default connect(mapStateToProps, mapDispatchToProps)(SearchedPage);


