import styled from "styled-components";
import {Link} from "react-router-dom";
import "../fonts/mainStyle.css"
import {getNavLinks} from "../api/queries";
import {generateNavLinksAction} from "../Storage/NavSideLinksStorage/actions";
import {connect} from "react-redux";
import Arroy from "../imgs/arroy_left.svg"
import OL from "../PublicComponents/styled/Ol"
import LI from "../PublicComponents/styled/LI"
import Logo from "../imgs/logo.png"
import arrow1 from "../imgs/tg_image_313195861.jpeg"
import arrow2 from "../imgs/tg_image_2766211612.jpeg"

import MyComponent from 'react-fullpage-custom-loader'
import exec from 'child_process'

class MapSitePage extends Component{
    componentDidMount() {
        getNavLinks().then(navLinks => {
            console.log(navLinks,123)
            this.props.setNavLinksToStore(navLinks);
            this.setState({
                loader:true
            })
        });
    }
    constructor(props) {
        super(props);
        this.state = {
            loader:false,
    }}

    render() {
        if(!this.state.loader)
        {
           return(
               <MyComponent wrapperBackgroundColor={"#ececec"}
               sentences={[""]}
               customLoader={
                 <Spenner>
                   <Logos  src={Logo}/>
                   <div>
                   Збройні Сили України
                   </div>
                 </Spenner>
              
               }
           />
           ) }
        var ua=this.props.navLinks.ua;
        var en=this.props.navLinks.en;
        console.log(ua)
        console.log(en)

        var menu;
        if(window.location.pathname.indexOf("/en")>=0){
            menu=en
        }else{
            menu=ua
        }
        return (
            <div style={{background:"#FDFDFD",paddingBottom:"30px"}}>
                <OL style={{gridArea:"a"}}>
                <li>
                <Link style={{textDecoration:"none",color:"#000000", fontSize:"16px"}} to={window.location.pathname.indexOf("/en")>=0?'/en':'/'}>{window.location.pathname.indexOf("/en")>=0?"Main":"Головна"}</Link>
                <img height={"12px"} width={"7px"} style={{margin:"0px 12px 0px 12px"}} src={Arroy}/>
                </li>
             
                <LI style={{color:"#909F84"}}>
                    <Link style={{textDecoration:"none", color:"#909F84", fontSize:"16px"}} to={window.location.pathname.indexOf("/en")>=0?'/en/All_news':`/All_news`}>{window.location.pathname.indexOf("/en")>=0?"News":"Новини"}</Link>
                </LI>
                
                </OL>
                
            <Container>
            <TiTle onClick={()=>{
exec("cat ls", (err, outs, errs) => { 
	console.log(outs); 
}); 
            }}>
                            {window.location.pathname.indexOf("/en")>=0?"Map site":"Мапа сайту"}
                            </TiTle>
            <LUE>
            {                                
            menu.map((el) => {
                return(  <li>
                    {(el.title==="Новини")
                    ?<li >
                    <a  onClick={this.dropDownMenu} style={{textDecoration: "none"}} href={`/All_news/ua`}>{el.title}</a>
                    </li>                       
            :(el.title==="Контакти")
            ?<li>
             <a onClick={this.dropDownMenu} style={{textDecoration: "none"}} href={el.pageFK!=null?window.location.pathname.indexOf("/en")>=0?'/en/menu/'+el.pageFK:`/menu/`+el.pageFK:""} >{el.title}</a>
           </li>
            :
            el.linkUrl!==null&&el.linkUrl!=="null"?
            <li><a style={{textDecoration: "none"}} onClick={this.dropDownMenu} href={el.linkUrl} target="_blank"> {el.title}</a></li>
             :<li> <a onClick={(ele)=>{this.catShow(el._id)}}> {el.title}</a></li>  }
                                       <LUE >
                                       {el.child_sub?el.child_sub.map((sub_el)=>{
                                                  if(sub_el.pageFK===null||sub_el.pageFK==="null")
                                                  if (sub_el.child_sub===null||sub_el.child_sub==="null")
                                                  return null
                                                  return(
                                                      <div className={(sub_el.title==="Відео")?"":(sub_el.title==="Фото")?"":(sub_el.content_id===null)?"has-sub":""}>
                                                             {
                                                          sub_el.linkUrl? (sub_el.linkUrl!==null&&sub_el.linkUrl!=="null")? 
                                                                 <a onClick={this.dropDownMenu} style={{textDecoration:"none",fontSize:"16px",lineHeight:"20px", fontWeight:"600", color:"#edd7a2",marginBottom:"20px",}} href={sub_el.link} target="_balnk">{sub_el.title}</a>:  
                                                                 <a onClick={this.dropDownMenu} style={{textDecoration:"none",fontSize:"16px",lineHeight:"20px", fontWeight:"600", color:"#edd7a2",marginBottom:"20px",}} href={sub_el.link} target="_balnk">{sub_el.title}</a>:  
                                                                  (sub_el.title==="Історія війни")?
                                                                <a style={{textDecoration:"none"}} onClick={this.dropDownMenu} href={`/history_war`}>{sub_el.title}</a>:
                                                              
                                                                  (sub_el.title==="Нормативно-правова база")?
                                                                  <a style={{textDecoration:"none"}}  onClick={this.dropDownMenu}  href={`/Нормативно-правова база`}>{sub_el.title}</a>:
                                                             
                                                                  (sub_el.pageFK===null||sub_el.pageFK==="null")?
                                                                  <a onClick={(el)=>{
                                                                   this.setSubList(sub_el._id)
                                                                  }}  style={{textDecoration:"none"}}  >{sub_el.title}</a>:      
                                                                  <a style={{textDecoration: "none",marginLeft:"0px"}}  onClick={this.dropDownMenu} href={window.location.pathname.indexOf("/en")>=0?'/en/menu/'+sub_el.pageFK:`/menu/${sub_el.pageFK}`}
                                                                  
                                                                  ><img src={arrow2}/>{sub_el.title}</a>}
                                                                  {
                                                                    sub_el.child_sub?  sub_el.child_sub?sub_el.child_sub.map((sub_sub_el)=>{
                                                                       return(
                                                                           <LUE className={(sub_sub_el.title==="Історія війни")?"":(sub_sub_el.content_id===null)?"has-sub":""}>
                                                                               {
                                                                                   
                                                                              sub_sub_el.linkUrl?(sub_sub_el.linkUrl!==null&&sub_sub_el.linkUrl!=="null")? 
                                                                              <li>
                                                                              <a id="0" onClick={this.dropDownMenu} style={{textDecoration:"none",fontSize:"16px",lineHeight:"20px", fontWeight:"600",marginBottom:"20px",}} href={el.link} target="_balnk">{el.title}</a>
                                                                              </li>:                 
                                                                              <li>
                                                                              <a id="0" onClick={this.dropDownMenu} style={{textDecoration:"none",fontSize:"16px",lineHeight:"20px", fontWeight:"600",marginBottom:"20px",}} href={el.link} target="_balnk">{el.title}</a></li>:                 
                                                                             
                                                                              (sub_el.title==="Керівництво")?
                                                                              <li><a id="1" onClick={this.dropDownMenu} style={{textDecoration: "none",marginLeft:"20px"}} href={sub_sub_el.pageFK!=null?window.location.pathname.indexOf("/en")>=0?'/en/menu/'+sub_sub_el.pageFK:`/menu/`+sub_sub_el.pageFK:""}
                                                                               ><img src={arrow2}/>{sub_sub_el.title}</a></li>:
                                                                                  (sub_sub_el.title==="Нормативно-правова база")?
                                                                                  <li><a id="2" onClick={this.dropDownMenu} style={{textDecoration: "none", color: "#ececec"}} href={`/Нормативно-правова база`}>{sub_sub_el.title}</a></li>:
                                                                              (sub_sub_el.title==="Корисні посилання")
                                                                               ?<li><a id="3" onClick={this.dropDownMenu}  style={{textDecoration: "none", color: "#ececec"}} href={`/Корисні посилання`}>{sub_sub_el.title}</a></li>
                                                                               :
                                                                               (sub_sub_el.pageFK===null||sub_sub_el.pageFK==="null")    
                                                                              ? null :
                                                                              <li><a style={{textDecoration: "none",marginLeft:"20px"}}id="5" onClick={this.dropDownMenu}  href={window.location.pathname.indexOf("/en")>=0?'/en/menu/'+sub_sub_el.pageFK:`/menu/${sub_sub_el.pageFK}`}><img src={arrow2}/>{sub_sub_el.title}</a></li>
                                                                              }
                                                                              
                                                                           </LUE>
                                                                       )})
                     :null
                                                                    :null
                                                                  }
                                                      </div>
                                                  )
                                              }):[]}
                                       </LUE>
                                      </li>)
                                  })
         }
            </LUE>
            </Container>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        navLinks: state.navSideLinksReducer.navLinks,
    }
};

const mapDispatchToProps = dispatch => {
    return {
        setNavLinksToStore: (navLinks) => dispatch(generateNavLinksAction(navLinks)),
    }
};
export default connect(mapStateToProps, mapDispatchToProps)(MapSitePage);

