import React, { Component } from "react";
import styled from "styled-components";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faEye, faMinusSquare, faPlusSquare} from "@fortawesome/free-solid-svg-icons";
import {faAutoprefixer} from "@fortawesome/free-brands-svg-icons";
import style from "../../fonts/mainStyle.css"
import { Link } from "react-router-dom";
import Iconeye from "../../imgs/eye";
import Iconfacebook from "../../imgs/facebook";
import Iconyoutube from "../../imgs/youtube";
import Icontwitter from "../../imgs/twitter";
import Iconinstagram from "../../imgs/instagram";
import Iconrss from "../../imgs/rss";
import { withRouter } from 'react-router-dom'


function handleClickPlus() {
   var body =document.body;
    document.body.style.fontSize=document.body.style.fontSize+2;
    var fs=(
            body.currentStyle||(window.getComputedStyle&&getComputedStyle(body,null))||
            body.style
        ).fontSize;
    var a =parseInt(fs.split("px")[0])+2;
    if (a>24)
    {a=24;}
    document.body.style.fontSize=a+"px";
        fs=
        (
            body.currentStyle||(window.getComputedStyle&&getComputedStyle(body,null))||
            body.style
        ).fontSize;
}
function handleClickMinus() {
    var body =document.body;
    document.body.style.fontSize=document.body.style.fontSize+2;
    var fs=(
        body.currentStyle||(window.getComputedStyle&&getComputedStyle(body,null))||
        body.style
    ).fontSize;
    var a =parseInt(fs.split("px")[0])-2;
    if (a<10)
    {a=10;}
    document.body.style.fontSize=a+"px";
    fs=
        (
            body.currentStyle||(window.getComputedStyle&&getComputedStyle(body,null))||
            body.style
        ).fontSize;
}
function handleClickAuto(){
    document.body.style=style;
}
class SpecialFeatures extends Component {
    componentDidMount(){

    }
    render() {
        const Button = withRouter(({ history }) => (
            <Iconrss width={40}   height={20} fill = "#47523F"
              onClick={(el) => { 
                history.push(`/All_news`) }}
            />
          ))
      return (
        <DisabledFeaturesContainer>

           {this.props.socialLinks.map(el=>
                {
                    if(el.title_en==="facebook")
                        return(
                                <a href={el.link} target="_blank">
                                    <Iconfacebook  width={40}   height={20} fill = {this.props.t?"#fdfdfd":"#47523f"}></Iconfacebook>
                                </a>
                            )
                        if(el.title_en==="twitter")
                            return(
                                <a href={el.link}  target="_blank">
                                    <Icontwitter  width={40}   height={20} fill = {this.props.t?"#fdfdfd":"#47523f"}></Icontwitter>
                                </a>
                            )
                        if(el.title_en==="youtube")
                            return(
                                <a href={el.link}  target="_blank">
                                    <Iconyoutube  width={40}   height={20} fill = {this.props.t?"#fdfdfd":"#47523f"}></Iconyoutube>
                                </a>
                            )
                            if(el.title_en==="instagram")
                            return(
                                <a href={el.link}  target="_blank">
                                    <Iconinstagram  width={40}   height={20} fill = {this.props.t?"#fdfdfd":"#47523f"}></Iconinstagram>
                                </a>
                            )
                }
            )}
            
                
           
            <a target="_blank"  href={"http://www.zsu.gov.ua/feed/rss"} >
                <Iconrss width={40}   height={20} fill = {this.props.t?"#fdfdfd":"#47523f"}></Iconrss>
            </a>
            <Iconeye  width={40}   height={20} fill = {this.props.t?"#fdfdfd":"#47523f"} style={{cursor:"pointer"}} onClick={()=>this.props.specialModeSwitch()} />

            {this.props.t?<div></div>:<div><a style={{textDecoration:"none",color:this.props.t?"#fdfdfd":"#47523f",}}   href={this.props.language?"/":"/en"} onClick={this.props.languageModeSwitch}>
                <p  style={{textDecoration:"none",margin:"0",fontSize:"14px",height:"20px",fontWeight:"bold"}}>
                    {this.props.language?'УКР':'ENG'}
                </p>
            </a></div>}
            {/*<div className="er" onClick={handleClickMinus}><FontAwesomeIcon icon={faMinusSquare} color = "#47523F"/></div>*/}
            {/*<div className="er" onClick={handleClickAuto}><FontAwesomeIcon icon={faAutoprefixer} size={"2x"} color = "#47523F"/></div>*/}
            {/*<div className="er" onClick={handleClickPlus}><FontAwesomeIcon className="dsa" icon={faPlusSquare} color = "#47523F"/></div>*/}

      </DisabledFeaturesContainer>

);
}


}
export default SpecialFeatures;

const DisabledFeaturesContainer = styled.div`
margin-right: 44px;
display:flex;
 
  
`;
