import React, {Component} from 'react';
import styled from 'styled-components';
import BurgerImg from '../../imgs/iconfinder_Menu_4473024.svg';
import {Link} from "react-router-dom";
import "../../fonts/mainStyle.css"
import "./index.css"
import SearchField from "./SerachField";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faSearch} from "@fortawesome/free-solid-svg-icons";
import {MAIN_URL, NEWS_URL, SIDE_LINKS_URL,FORCES_URL} from "../../api/URLs";
import { isThisSecond } from 'date-fns';
import { withRouter } from 'react-router-dom'

export default class NavBar extends Component{
    lastScroll = 0;
    constructor(props){
        super(props);
    }
       


   
    renderDropdowns(data){
        return data.map((el) => {
              return(  <li id={el.title} onMouseEnter={() => {
                this.props.toggleActiveDropdown(el.submenu_item,el.child_sub,el.title)
            }}
            
            onMouseOut={(el)=>{
                if(el.target.id==="men"||el.target.id==="dropdown")
                {

                }else{
                    this.props.toggleDisDropdown()
                }
            }}
            onMouseOver={(el)=>{
                if(el.target.id==="men"||el.target.id==="dropdown")
                {

                }else{
                    this.props.toggleDisDropdown()
                }
            }}
            onClick={() => {
                this.props.toggleActiveDropdown(el.child_sub,el.child_sub,el.title)
            }}
            onMouseOutCapture={(el)=>{
            }}
            >     
                  {(el.title==="Новини")
            ?<A id="men"    href={`/All_news/ua`}>{el.title}</A>
            :(el.title==="Контакти")
            ? <A id="men" style={{textDecoration: "none", }} href={el.pageFK!=null?window.location.pathname.indexOf("/en")>=0?`/en/menu/`+el.pageFK:`/menu/`+el.pageFK:""} >Контакти</A>
            :
            el.linkUrl!==null&&el.linkUrl!=="null"?<A id="men" href={el.linkUrl
            } target="_blank"> {el.title}</A>:<AE id="men" style={{color:this.props.activeDropdown===el.child_sub?"#edd7a2":"",background:this.props.activeDropdown===el.child_sub?"rgb(71, 82, 63)":""}} href={window.location.pathname.indexOf("/en")>=0?'/en/menu/'+el._id:'/menu/'+el._id}> {el.title}</AE>    }
                </li>)
            }
        )
    }






    render() {
        return(
            <div 
            onMouseOverCapture={(el)=>{
                if(el.target.id==="men"||el.target.id==="dropdown")
                {

                }else{
                    this.props.toggleDisDropdown()
                }
            }}
            onMouseOutCapture={(el)=>{
            }}
            onMouseOut={(el)=>{
                if(el.target.id==="men"||el.target.id==="dropdown")
                {

                }else{
                    this.props.toggleDisDropdown()
                }
            }}
            onMouseOver={(el)=>{
                if(el.target.id==="men"||el.target.id==="dropdown")
                {

                }else{
                    this.props.toggleDisDropdown()
                }
            }}>
                <NavBarContainer 
       
                onMouseOut={(el)=>{
                    if(el.target.id==="men"||el.target.id==="dropdown")
                    {

                    }else{
                        this.props.toggleDisDropdown()
                    }
                }}
                onMouseOver={(el)=>{
                    if(el.target.id==="men"||el.target.id==="dropdown")
                    {
    
                    }else{
                        this.props.toggleDisDropdown()
                    }
                }}
            >
                <NavBarDropdownWrapper>
                        <Menu id='cssmenu' >
                            <ul>
                                {this.renderDropdowns(this.props.navLinks)}
                            </ul>
                        </Menu>
                </NavBarDropdownWrapper >
                <DropDown  >
                {
                        this.props.activeDropdown!=null?this.props.activeDropdown.length===0?<div></div>:<MenuDropDown onMouseLeave={(el)=>{
                            this.props.toggleDisDropdown()
                            }
                        } style={{background:"#47523F"}}  id="dropdown"   >
                            {                                
                                this.props.activeDropdown.map(sub_el=>{
                                    if(sub_el.title==="Історія Війни"){
                                    }
                                    if(sub_el.pageFK===null||sub_el.pageFK==="null")
                                        if (sub_el.child_sub===null||sub_el.child_sub==="null")
                                        return null
                                    return(
                                       <div id="men">
                                           <LI2 id="men" >
                                           {(sub_el.linkUrl!==null&&sub_el.linkUrl!=="null")? 
                                           <a id="men" style={{textDecoration:"none", color:"#edd7a2"}} href={sub_el.link} target="_balnk">{sub_el.title}</a>:
                                            (sub_el.pageFK===null||sub_el.pageFK==="null")?
                                                (sub_el.child_sub===null||sub_el.child_sub==="null")
                                                ?null
                                                : <a id="men" style={{textDecoration:"none", color:"#edd7a2"}} href={window.location.pathname.indexOf("/en")>=0?'/en/menu/'+sub_el._id:`/menu/`+sub_el._id} >{sub_el.title}</a>
                                                :<a id="men" href={window.location.pathname.indexOf("/en")>=0?'/en/menu/'+sub_el.pageFK:`/menu/`+sub_el.pageFK} style={{textDecoration:"none", color:"#edd7a2"}} >{sub_el.title}</a>
                                            }
                                           </LI2>
                                           <ul id="men">
                                                {sub_el.child_sub?sub_el.child_sub.map(el=>{
                                                   return(
                                                        <LI3 id="men" >
                                                            {
                                                            el.linkUrl?(el.linkUrl!==null&&el.linkUrl!=="null")? 
                                                            <a id="men" onClick={(el) => {
                                                                this.props.toggleDisDropdown()
                                                                     }}  style={{textDecoration:"none"}} href={el.link} target="_balnk">{el.title}</a>:                 
                                                            <a id="men" onClick={(el) => {
                                                                this.props.toggleDisDropdown()
                                                                     }}  style={{textDecoration:"none"}} href={el.link} target="_balnk">{el.title}</a>:                 
                                                            (sub_el.title==="Керівництво")?
                                                            (el.pageFK===null||el.pageFK==="null")  ?<a  href={window.location.pathname.indexOf("/en")>=0?'/en/menu/'+el._id:`/menu/`+el._id} id="men" style={{textDecoration: "none", color: "#ececec"}}>{el.title}</a>:
                                                            <Link id="men" onClick={(el) => {
                                                                this.props.toggleDisDropdown()
                                                                     }} style={{textDecoration: "none", color: "#ececec"}} to={{pathname:window.location.pathname.indexOf("/en")>=0?'/en/menu/'+el.pageFK:`/menu/`+el.pageFK,
                                                                }} >{el.title}</Link>:
                                                                (el.title==="Нормативно-правова база")?
                                                                <Link id="men" onClick={(el) => {
                                                                    this.props.toggleDisDropdown()
                                                                         }}  style={{textDecoration: "none", color: "#ececec"}} to={`/Нормативно-правова база`}>{el.title}</Link>:
                                                            (el.title==="Корисні посилання")
                                                            ?<Link id="men" onClick={(el) => {
                                                                this.props.toggleDisDropdown()
                                                                     }} style={{textDecoration: "none", color: "#ececec"}} to={`/Корисні посилання`}>{el.title}</Link>
                                                            :
                                                            (el.pageFK===null||el.pageFK==="null")    
                                                                ?  null     //<a  id="men"  style={{textDecoration: "none", color: "#ececec"}}>{el.title}</a>
                                                                :<Link id="men" onClick={(el) => {
                                                                    this.props.toggleDisDropdown()
                                                                         }} style={{textDecoration: "none", color: "#ececec"}} to={{pathname:window.location.pathname.indexOf("/en")>=0?'/en/menu/'+el.pageFK:`/menu/`+el.pageFK,
                                                                                     state: {
                                                                                            id: el._id,
                                                                                             title: sub_el.title,
                                                                                            sub_el: sub_el.child_sub,
                                                                                            el_before:[{"title":this.props.title},{"title":sub_el.title}],
                                                                                            from:"men"
                                                                                            }
                                                                            }}>{el.title}</Link>}
                                                            </LI3>
                                                   )
                                                }):<div></div>}
                                           </ul>
                                       </div>
                                    )
                                })
                            }
                        </MenuDropDown>
                :<div></div>    
                }
                </DropDown>
            </NavBarContainer>
        
            </div>
        
        )
    }
}
const AE =styled.a`
`
const A =styled.a`
&:hover{
    background: #909f84 !important;
}
`
const LI3 =styled.li`
text-decoration: none;
 list-style:none;
 font-size:16px;
 line-hight:20px;
  font-weight:400; 
  color:#ececec;
  margin-bottom:20px;
  @media only screen and (max-width: 1023px){
 font-size:12px;
     
     
}
`
const LI2 =styled.li`
text-decoration: none;
 list-style:none;
 font-size:16px;
 line-hight:20px;
  font-weight:400; 
  color:#edd7a2;
  margin-bottom:20px;
  @media only screen and (max-width: 1023px){
 font-size:12px;
     
     
    }
`
const SearchButton = styled.div` 
    transition: 400ms ease-out;
    padding: 3px;
    width:30px;
    height:30px;
    border-radius: 6px;
    font-size:20px;
    cursor: pointer;
    flex-grow: 1;
    text-align: center;
    
`;

const MenuDropDown = styled.div`
background: rgb(71, 82, 63);
    max-width: 1400px;
    margin: auto;
    padding: 48px 32px 48px 32px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 16px;
`
const SearchDropDown = styled.div`
padding: 8px 48px 8px 48px;
display: grid;
grid-template-columns:1fr 1fr 1fr;
`
const DropDown=styled.div`
background: rgb(71, 82, 63);


    @media only screen and (max-width: 1023px){
        display: none;
     
    }
    
  

`;
const Menu =styled.div`
padding: 0; margin: 0; border: 0; width:100%
`;
const SearchFiledWrapper = styled.div`
  margin-top:auto;
  margin-bottom:auto;
margin-right: 15px;
@media screen and (max-width: 639px){
    display: none;
}

`;
const NavBarContainer = styled.div`
 
   
`;

const NavBarToggle = styled.img`
    display: none;
    cursor: pointer;
    color:#ffffff;
    background:#ffffff;
    @media screen and (max-width: 639px){
        display: inline-block;
        height: 40px;
    }
`;

const NavBarDropdownWrapper = styled.div`
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-webkit-box-align: center;
-ms-flex-align: center;
align-items: center;
max-width: 1400px;
margin: 0 auto;
    @media screen and (max-width: 1023px){
        display: ${props => props.opened ? "inline-block" : "none"};
        top: 50px;
        left: 0;
        width: 100%;
        margin:0px;
        color:#000000;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        background-color: #ececec;
    }
`;

const NavDropdownTitleWrapper = styled.div`
    position: relative;
`;

const NavDropdownTitle = styled.div`
    cursor: pointer;
    position: relative;
    transition: 500ms ease-out;
   
    @media screen and (min-width: 1024px){
        padding: 1px;
        :after{
            content: "";
            background-color: #d50663;
            transition: 500ms;
            position: absolute;
            left: 50%;
            bottom: 0;
            transform: translateX(-50%);
            height: 10%;
        }
        :hover{
            :after{
                width: 100%;
            }
        }
    }
    @media screen and (max-width: 1023px){
        background-color: rgba(0,0,0,0.5);
        padding: 10px;
    }
`;

const NavBarDropdown = styled.span`
`;

const NavBarDropdownItem = styled.div`
    display: block;
    position: relative;
    transition: 400ms;
     @media screen and (min-width: 1024px){
     padding-top: 15px;
    padding-bottom:15px;
    }
    @media screen and (max-width: 1023px){
    padding-left:100px;
     padding-top: 8px;
    padding-bottom:8px;
    }
    > a{
        text-decoration: none;
        color: white;
    }
    :hover{
        background-color: #ebebeb;
        color: black;
    }
`;