import React, {Component, Fragment} from "react";
import styled from "styled-components";
import {Link} from "react-router-dom";
import "../../fonts/mainStyle.css"
import {getMenuById} from"../../api/queries";
import {generatePageReceivedAction} from "../../Storage/MenuPageStorage/actions";
import {connect} from "react-redux";
import logo from "../../PublicComponents/assets/img/logo.png"
import NoImage from "../../imgs/ghzsy.png";
import Arroy from "../../imgs/arroy_left.svg"
import OL from "../../PublicComponents/styled/Ol"
import LI from "../../PublicComponents/styled/LI"
import MyComponent from 'react-fullpage-custom-loader'
import Logo from "../../imgs/logo.png"
import GhZsu from "../../imgs/ghzsy.png"
import Paginations from './Pagination'
import {ADIS,AACT} from "../../PublicComponents/styled/A"

let limit=9;
let offset=0;
let fotos=[];
let pag=[]
class FotoPage extends Component{
    constructor(props){
        super(props);
        this.state = {
            id:"",
            loading: true,
            isOpen: false,
            image:"",
            allCountries: [],
            currentCountries: [],
            currentPage: null,
            totalPages: null
        };
    }
    componentDidMount() {
        const {id} = this.props.location.state|| {}
        getMenuById(this.props.match.params.id).then(page => {
            var cat;
          page.map(el=>{
              if(id===el._id){
                  cat=el
              }
          })
          fotos=cat.photos.reverse()
            this.props.setPageToStore(fotos.slice(0,limit))
            pag=cat.photos.slice(0,((cat.photos.length/9)+((cat.photos.length%9>0)?1:0)))
           this.setState({
            loading:false,
            id:cat.title_ua
           })
        })
    
        
        
    }
    handleShowDialog = (image) => {
        this.setState({ isOpen: !this.state.isOpen,image:image });
      };
      onPageChanged = data => {
        const { allCountries } = this.state;
        const { currentPage, totalPages, pageLimit } = data;
    
        offset = (currentPage - 1) * pageLimit;
        this.props.setPageToStore(fotos.slice(offset,offset+limit))
        this.setState({ currentPage, totalPages });
      };
    
    renderFoto(){
        var cat;
          return this.props.page.map((el) => {
            
                return (
                    
                    <NewsItemWrapper >
                        <NewsImgCont>
                            <ContImage>
                            <NewsImg id={el} onClick={()=>{
                          this.handleShowDialog(el)
                        }} src={`/image/${el}`}/>
                            </ContImage>
                        </NewsImgCont>
                       
                        
                    </NewsItemWrapper>
                )
   
            
           
       }
       
  )
    }

    render() {
var count =0;

        if(this.state.loading)
        {
           return(
               <MyComponent wrapperBackgroundColor={"#ececec"}
               sentences={[""]}
               customLoader={
                 <Spenner>
                   <Logos  src={Logo}/>
                   <div>
                   Збройні Сили України
                   </div>
                 </Spenner>
              
               }
           />
           ) }
        window.scrollY=0
        var body = document.body;
        body.scroll(0.0,0.0)
        var cat;
        
        return (
            <div style={{background:"#FDFDFD",paddingBottom:"30px", textAlign: "left!important"}}>

{this.state.isOpen && (
<div style={{
    width: "100%",
    height: "100%",
    left: "0",
    top: "0",
    zIndex: "1042",
    overflow: "hidden",
    position: "fixed",
    background: "#0b0b0b",
    opacity: ".8",
}}></div>
)}
                {this.state.isOpen && (
          <dialog
            className="dialog"
            style={{ position: 'absolute' }}
            open
            onClick={this.handleShowDialog}
          >
            <MFPWrap >
                <MFPContainer >
                    <MFPContent >
                        <MFPFigure >
                            <Button title="Close (Esc)" type="button"  onClick={this.handleShowDialog}>×</Button>
                            <Figure>
                                <Imge src={"/image/"+this.state.image} />
                                   
                                                    </Figure>
                                                    </MFPFigure>
                                                    </MFPContent>
                                                    </MFPContainer>
                                                    </MFPWrap>
          </dialog>
        )}
                <OL style={{gridArea:"a"}}>
                <li style={{display: "contents"}}>
                <ADIS href="/">{this.props.language?"Main":"Головна"}</ADIS>
                <img height={"12px"} width={"7px"} style={{margin:"6px 12px 6px 12px"}} src={Arroy}/>

                </li>
             
                <LI style={{display: "contents"}}>
                <ADIS  >{this.props.language?"Media":"Медіа"}</ADIS>
                <img height={"12px"} width={"7px"} style={{margin:"6px 12px 6px 12px"}} src={Arroy}/>

                </LI>
                <LI style={{display: "contents"}}>
                <ADIS  href={`/menu/photo-galery`}>Фото</ADIS>
                <img height={"12px"} width={"7px"} style={{margin:"6px 12px 6px 12px"}} src={Arroy}/>

                </LI>
                <LI style={{color:"#909F84", fontSize:"16px"}}>
                <AACT>{this.state.id}</AACT>
                </LI>
                
                </OL>
               <PrimaryNewsWrapper>
              <Containerer>
              <Title>{this.state.id} </Title>
              <UL>
              {this.renderFoto()}
              </UL>
              <Paginations
                            totalRecords={fotos.length}
                            pageLimit={9}
                            pageNeighbours={1}
                            onPageChanged={this.onPageChanged}
                        />
            </Containerer>
            </PrimaryNewsWrapper>
                        </div>
        );
    }
}

let mapStateToProps = state => {
    return {
        page: state.menuPageReducer.page,

    }
};

let mapDispatchToProps = dispatch => {
    return {
        setPageToStore: page => dispatch(generatePageReceivedAction(page)),


    }
};

export default connect(mapStateToProps, mapDispatchToProps)(FotoPage);
