import styled from "styled-components";

const OL =styled.ul`  
list-style: none;
display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    padding-top: 32px;
    padding-bottom: 48px;
    max-width: 1400px;
    margin: 0 auto;
    padding-left: 32px;
    padding-right: 32px;
    @media only screen and (max-width: 1020px)
     {
        padding-top: 16px;
        padding-left: 16px;
        padding-right: 16px;
    }
    
    
`;export default OL;